import {Utils} from '../../store/util'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      sid: '',
      step: 1,
      mobile: '',
      img_code: '',
      mobile2: '',
      img_code2: '',
      checkcode: '',
      verify_code: '',
      time: 60,
      newPwd: '',
      repeatPwd: '',
      dialogMsg: '',
      dialogVisible: false,
        domain: ''
    }
  },
  directives: {
    TransferDom
  },
  components: {
    XHeader,
    Group,
    XInput,
    XButton,
    TransferDom,
    XDialog
  },
  created () {
      document.title = '忘记密码'
      this.$nextTick(()=>{
          this.getDomain ()
      })
    this.sid = Math.random()
  },
  methods: {
    changeImgCode () {
        this.sid = Math.random()
    },
    submit1 () {
      if (this.mobile === '') {
          this.dialogMsg = '请输入手机号'
          this.dialogVisible = true
      } else if (!/^[0-9]{11}$/.test(this.mobile)) {
          this.dialogMsg = '请输入正确的手机号'
          this.dialogVisible = true
      } else if (this.img_code === '') {
          this.dialogMsg = '请输入验证码'
          this.dialogVisible = true
      } else {
        let form = new FormData()
        form.append('step', 1)
        form.append('mobile', this.mobile)
        form.append('img_code', this.img_code)
        this.$store.dispatch('forgotPwd', form).then((res) => {
          this.$router.push('/forgot-pwd?k=' + res.msg)
          this.step = 2
          this.sid = Math.random()
          this.mobile2 = this.encryptPhone(this.mobile)
          this.getVeirfyCode()
        }).catch((res) => {
          this.dialogMsg = res
          this.dialogVisible = true
          this.changeImgCode()
        })
      }
    },
      getDomain () {
          this.$store.dispatch('passport_getDomain', {}).then((res) => {
              this.domain = res
          }).catch((res) => {
          })
      },
    encryptPhone (phone) {
      let str = phone.substring(0, 3) + '*****' + phone.substring(8)
      return str
    },
    getVeirfyCode () {
      this.$store.dispatch('getVeirfyCode').then((res) => {
        this.verify_code = res
      }).catch((res) => {
        this.dialogMsg = res.errmsg
        this.dialogVisible = true
      })
    },
    sendCode () {
      if (this.img_code2 === '') {
          this.dialogMsg = '请输入图片验证码'
          this.dialogVisible = true
      } else {
        this.$store.dispatch('sendSms', {type: 'mobilephone',checkcodeimg: this.img_code2, mobilephone: this.mobile, message: '_findpwd_', verify_code: this.verify_code}).then((res) => {
          this.timeCount()
        }).catch((res) => {
          this.dialogMsg = '发送短信验证码失败！' + res
          this.dialogVisible = true
        })
      }
    },
    timeCount () {
      let utils = new Utils()
      this.time--
      let count = window.setInterval(() => {
        this.time--
        utils.setCookie('time_findpwd_cookie', this.time, 1)
        if (this.time === 0) {
          window.clearInterval(count)
          this.time = 60
          utils.clearCookie('time_findpwd_cookie')
        }
      }, 1000)
    },
    submit2 () {
      if (this.img_code2 === '') {
          this.dialogMsg = '请输入图片验证码'
          this.dialogVisible = true
      } else if (this.checkcode === '') {
          this.dialogMsg = '请输入短信验证码'
          this.dialogVisible = true
      } else {
        let form = new FormData()
        form.append('step', 2)
        form.append('mobile', this.mobile)
        form.append('checkcode', this.checkcode)
        this.$store.dispatch('forgotPwd', form).then((res) => {
          this.$router.push('/forgot-pwd?k=' + res.msg)
          this.step = 3
        }).catch((res) => {
          this.dialogMsg = res
          this.dialogVisible = true
        })
      }
    },
    submit3 () {
      if (this.newPwd === '') {
          this.dialogMsg = '请设置密码'
          this.dialogVisible = true
      } else if (!/^[0-9a-zA-Z]{8,18}$/.test(this.newPwd)) {
          this.dialogMsg = '8-18位字母、数字或者英文'
          this.dialogVisible = true
      } else if (this.repeatPwd === '') {
          this.dialogMsg = '请再次输入密码'
          this.dialogVisible = true
      } else if (this.newPwd !== this.repeatPwd) {
          this.dialogMsg = '两次密码输入不一致'
          this.dialogVisible = true
      } else {
        let form = new FormData()
        form.append('step', 3)
        form.append('mobile', this.mobile)
        form.append('pwd', this.newPwd)
        this.$store.dispatch('forgotPwd', form).then((res) => {
          this.$router.push('/')
        }).catch((res) => {
          this.dialogMsg = res
          this.dialogVisible = true
        })
      }
    }
  }
}



