import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import statusBar from '../home/statusBar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
      return {
          title: '',
          dialogShow: false,
          dialogMsg: '',
          dialogLink: '',
          order_type: '',
          step: 1,
          button_msg: '',
          barType: '',
          goods_name: '',
          content_all: {
              desc1: '',
              time1: '',
              desc2: '',
              time2: '',
              desc3: '',
              time3: ''
          }
      }
  },
  computed: {
      ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
      Group,
      Flexbox,
      FlexboxItem,
      XButton,
      statusBar,
      XDialog
  },
  created () {
    document.title = '中子星金融'
    this.$nextTick(() => {
        this.getDetail()
    })
  },
  activated () {
      document.title = '中子星金融'
      this.$nextTick(() => {
          this.getDetail()
      })
  },
  methods: {
      getDetail () {
          this.$store.dispatch('sinapay_index', {'trade_type':this.$route.query.trade_type,'trade_no':this.$route.query.trade_no,'sign': this.$route.query.sign}).then((res) => {
              this.barType = res.barType
              this.step = res.step
              this.title = res.title
              this.button_msg = res.button_msg
              this.content_all.time1 = res.time1
              this.content_all.time2 = res.time2
              this.content_all.time3 = res.time3
              this.content_all.desc1 = res.desc1
              this.content_all.desc2 = res.desc2
              this.content_all.desc3 = res.desc3
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogShow = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      }
  }
}



