import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
export default {
    data () {
        return {
            title: '',
            dialogMsg: '',
            dialogLink: '',
            inlink: true,
            dialogVisible: false,
            confirmVisible: false,
            confirmMsg: '',
            confirmLink:'',
            product: {
                buyopen: false
            },
            payPassword:[]
        }
    },
    computed: {
        productId () {
            return this.$route.params.id
        }
    },
    directives: {
        TransferDom
    },
    components: {
        XButton,
        XDialog,
        Confirm
    },
    created () {
        document.title = '银行理财产品详情'
        window.scroll(0, 0)
        this.$nextTick(() => {
            this.getBankFinance()
        })
    },
    methods: {
        onConfirm(){
            if(!this.confirmLink){
                this.dialogMsg = '未通过企业商户资质审核'
                this.dialogVisible = true
                this.confirmVisible = false
                return
            }
            window.open(this.confirmLink,'_blank')
        },
        jump(path,query){
            this.$store.dispatch('getUserStatus').then((res) => {
                if(this.payPassword.isPayPassword){
                    this.$router.push({path: path, query: query})
                }else{
                    this.confirmMsg = this.payPassword.msg
                    this.confirmVisible = true
                    this.confirmLink = this.payPassword.url
                }
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogVisible = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                } else if (res.errcode === 40002) {
                    this.dialogLink = '/register2'
                }else{
                    this.dialogLink = ''
                }
            })
        },
        goHetong () {
            this.$router.push({path: '/hqhetong', query: { bd_code: this.product.bd_code }})
        },
        getBankFinance () {
            this.product = {}
            this.$store.dispatch('getBankFinance').then((res) => {
                this.product = res
                this.payPassword = res.payPassword
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogVisible = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                } else if (res.errcode === 40002) {
                    this.dialogLink = '/register2'
                } else {
                    this.dialogLink = ''
                }
            })
        }
    }
}



