export default {
  name: 'dilaog',
  props: ['show', 'message'],
  data () {
    return {
      display: 'none'
    }
  },
  mounted () {
    this.disp(this.show)
  },
  methods: {
    disp (val) {
      if (val) {
        this.display = 'block'
      } else {
        this.display = 'none'
      }
    }
  },
  watch: {
    'show': function (newval, oldval) {
      this.disp(newval)
    }
  }
}



