import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import statusBar from '../home/statusBar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
export default {
    data () {
        return {
            title: '',
            dialogShow: false,
            dialogMsg: '',
            dialogLink: '',
            order_type: '',
            step: 1,
            content_all: {
                desc1: '',
                time1: '',
                desc2: '预计到达余额账户',
                time2: ''
            }
        }
    },
    computed: {
        ...mapGetters(['company'])
    },
    directives: {
        TransferDom
    },
    components: {
        Group,
        Flexbox,
        FlexboxItem,
        XButton,
        statusBar,
    },
    created () {
        document.title = '中子星金融'
        this.$nextTick(() => {
            this.getDetail()
        })
    },
    methods: {
        getDetail () {
            let interface1 = 'bao_shuhui_return'
            if(parseInt(this.$route.query.yuyue) === 1){
                interface1 = 'bao_yuyue_shuhui_return'
            }
            if(parseInt(this.$route.query.dingqi) === 1){
                interface1 = 'bao_regular_redeem_return'
            }
            if(parseInt(this.$route.query.bill) === 1){
                interface1 = 'bao_bill_redeem_return'
            }
            let form = new FormData()
            form.append('order_no', this.$route.query.order_no)
            form.append('sn', this.$route.query.sn)
            this.$store.dispatch(interface1, form).then((res) => {
                    if(res.is_yuyue){
                       this.title = '预约申请赎回成功！'
                        this.content_all.desc1 = '预约申请赎回'
                    } else {
                        this.title = '申请赎回成功！'
                        this.content_all.desc1 = '申请赎回'
                    }
                  this.content_all.time1 = res.create_time
                  this.content_all.time2 = res.daozhang_date
                  if(res.payee_account_desc){
                      this.content_all.desc2 = '预计到达'+ res.payee_account_desc
                  }

                  if(res.status_code === '200'){
                      this.step = 2
                  }
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogShow = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                }
            })
        }
    }
}



