import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
    data () {
        return {
            title: '',
            dialogMsg: '',
            dialogLink: '',
            dialogVisible: false,
            product: {}
        }
    },
    computed: {
    },
    directives: {
        TransferDom
    },
    components: {
        Group,
        Cell,
        XDialog
    },
    created () {
        document.title = '已到期 / 赎回'
        window.scroll(0, 0)
        this.$nextTick(() => {
            this.getExpireProduct()
        })
    },
    methods: {
        goDetail (goods_type, outTradeNo) {
            this.$router.push({path: '/account/rebackDetail', query: {goods_type: goods_type, outTradeNo: outTradeNo }})
        },
        getExpireProduct () {
            this.product = {}
            this.$store.dispatch('getExpireProduct').then((res) => {
                this.product = res
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogVisible = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                } else if (res.errcode === 40002) {
                    this.dialogLink = '/register2'
                }else{
                    this.dialogLink = ''
                }
            })
        }
    }
}



