export default {
  props: {
    disp: {
      default: false
    }
  },
  data () {
    return {
      disp_style: {
        display: 'none',
        opacity: 0
      }
    }
  },
  mounted () {
    this.ctl_disp(this.disp)
  },
  methods: {
    ctl_disp (val) {
      if (val) {
        this.disp_style.display = 'block'
        setTimeout(() => { this.disp_style.opacity = 1 }, 100)
      } else {
        this.disp_style.opacity = 0
        setTimeout(() => { this.disp_style.display = 'none' }, 300)
      }
    }
  },
  watch: {
    'disp': 'ctl_disp'
  }
}



