import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import arrowImg from '../../assets/images/arrow-right.png'
import navMenu from '@/components/home/Nav'
import logoImg from '../../assets/images/logo.png'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      showNav: false,
      logoImg: logoImg,
      selectTagFilter: false,
      selectTagOrder: false,
      arrowImg: arrowImg,
      sort: '', // 排序
      project_status: '', // 项目状态
      project_period: '', // 项目期限
      profit_rate: '', // 收益率
      bank_type: '', // 票据类型
      products: [],
      page: 1,
      more: 1,
      dialogMsg: '',
      dialogVisible: false,
      dialogLink: '',
    }
  },
  directives: {
      TransferDom
  },
  components: {
    navMenu,
    XHeader,
    Grid,
    GridItem,
    XDialog
  },
  mounted () {
      document.title = '银行票据理财'
      window.scroll(0, 0)
      this.$nextTick(() => {
          this.getPage()
      })
  },
  methods: {
    jump(path,query){
        this.$store.dispatch('getUserStatus').then((res) => {
            this.$router.push({path: path, query: query})
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    clear () {
      this.project_status = ''
      this.project_period = ''
      this.profit_rate = ''
      this.bank_type = ''
    },
      getPage () {
          let filter = this.project_status + ',' + this.project_period + ',' + this.profit_rate + ',' + this.sort + ',' + this.bank_type
          this.$store.dispatch('product_product_bill', {'filter': filter, 'page': this.page}).then((res) => {
              this.products = this.products.concat(res)
          }).catch((res) => {
              if (res.errcode === 1) {
                  this.more = 0
              }
          })
      },
      nextPage () {
          this.page++
          this.getPage()
      },
      filterProduct () {
          this.page = 1
          this.products = []
          this.getPage()
      }
  }
}



