import {Utils} from '../../store/util'
import XHeader from 'vux/src/components/x-header/index.vue'
import loginImg from '../../assets/images/login-logo.png'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      loginImg: loginImg,
      agree: false,
      nick_name: '',
      phone: '',
      checkcode: '',
      user_pwd: '',
      time: 60,
      errmsg: '',
      dialogMsg: '',
      dialogVisible: false
    }
  },
  directives: {
    TransferDom
  },
  components: {
    XHeader,
    Group,
    XInput,
    XButton,
    CheckIcon,
    TransferDom,
    XDialog
  },
  created () {
      document.title = '注册'
      let utils = new Utils()
      let timeCookie = utils.getCookie('time_cookie')
      if (timeCookie) {
          this.time = timeCookie
          this.timeCount()
      }
  },
  methods: {
    register () {
        if (this.nick_name === '') {
            this.errmsg = '*请输入企业名称'
        } else if (this.phone === '') {
            this.errmsg = '*请输入手机号！'
        } else if (!/^[0-9]{11}$/.test(this.phone)) {
            this.dialogMsg = '请输入正确的手机号'
            this.dialogVisible = true
        } else if (this.checkcode === '') {
            this.errmsg = '*请输入验证码'
        } else if (this.user_pwd === '') {
            this.errmsg = '*请输入密码'
        } else {
            let form = new FormData()
            form.append('nick_name', this.nick_name)
            form.append('phone', this.phone)
            form.append('checkcode', this.checkcode)
            form.append('user_pwd', this.user_pwd)
            this.$store.dispatch('register', form).then((res) => {
                this.$router.push('/register2')
            }).catch((res) => {
                this.dialogMsg = '注册失败！' + res
                this.dialogVisible = true
            })
        }
    },
    sendCode () {
        if (this.nick_name === '') {
            this.dialogMsg = '请输入企业名称'
            this.dialogVisible = true
        } else if (this.phone === '') {
            this.dialogMsg = '请输入手机号'
            this.dialogVisible = true
        } else if (!/^[0-9]{11}$/.test(this.phone)) {
            this.dialogMsg = '请输入正确的手机号'
            this.dialogVisible = true
        } else {
            this.$store.dispatch('sendSms', {type: 'mobilephone', mobilephone: this.phone, message: '_register_', verify_code: 'zhongzixing'}).then((res) => {
                this.timeCount()
            }).catch((res) => {
                this.time = 60
                this.dialogMsg = '发送验证码失败！' + res
                this.dialogVisible = true
            })
        }
    },
    timeCount () {
      let utils = new Utils()
      this.time--
      let count = window.setInterval(() => {
        this.time--
        utils.setCookie('time_cookie', this.time, 1)
        if (this.time === 0) {
          window.clearInterval(count)
          this.time = 60
          utils.clearCookie('time_cookie')
        }
      }, 1000)
    }
  }
}



