import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import arrowImg from '../../assets/images/arrow-right.png'
import navMenu from '@/components/home/Nav'
import logoImg from '../../assets/images/logo.png'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      showNav: false,
      logoImg: logoImg,
      selectTag: 2,
      arrowImg: arrowImg,
      huoqi: [],
      dingqi: [],
      dialogMsg: '',
      dialogVisible: false,
      dialogLink: '',
    }
  },
  directives: {
      TransferDom
  },
  components: {
    navMenu,
    XHeader,
    Grid,
    GridItem,
    XDialog
  },
  mounted () {
      document.title = '主动管理型产品'
      window.scroll(0, 0)
      this.$nextTick(() => {
          this.product_index()
      })
  },
  methods: {
      jump(path,query){
          this.$store.dispatch('getUserStatus').then((res) => {
              this.$router.push({path: path, query: query})
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      product_index () {
          this.$store.dispatch('product_index', {}).then((res) => {
              this.huoqi = res.huoqi
              this.dingqi = res.dingqi
          }).catch((res) => {
          })
      }
  }
}



