'use strict'
import * as types from './types'

export const mutations = {
  [types.SET_COMPANY_INFO] (state, obj) {
    state.company = obj
  },
  [types.SET_LOADING] (state, val) {
    if (val) {
      state.loading++
    } else {
      state.loading--
    }
    if (state.loading < 0) state.loading = 0
  }
}



