var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"asurance white-container"},[_c('section',[_c('h1',[_vm._v("风险告知书")]),_vm._v(" "),_c('div',{staticClass:"text"},[_c('p',{attrs:{"align":"left"}},[_vm._v("尊敬的客户： "),_c('br'),_vm._v("\n        在购买投资产品之前，请您仔细阅读如下风险提示，并按照您的实际资产情况与风险承受能力，审慎作出投资决策。 "),_c('br'),_vm._v("\n        产品收益权对应产品投资领域为证券市场，本产品收益权面临包括但不限于以下风险： "),_c('br'),_vm._v("\n        一、产品风险 "),_c('br'),_vm._v("\n        本产品收益权对应的产品存在包括但不限于下述风险。产品层面面临的各项风险均会相应影响产品收益权的收益水平。\n      ")]),_vm._v(" "),_c('p',{attrs:{"align":"left"}},[_vm._v("（一）市场风险 "),_c('br')]),_vm._v("\n        证券市场价格因受各种因素的影响而引起的波动，将使产品资产面临潜在的风险。市场风险可以分为股票投资风险、债券投资风险与期货投资风险。 "),_c('br'),_vm._v("\n        1、股票投资风险主要包括： "),_c('br'),_vm._v("\n        （1）国家货币政策、财政政策、产业政策等的变化对证券市场产生一定的影响，导致市场价格水平波动的风险； "),_c('br'),_vm._v("\n        （2）宏观经济运行周期性波动，对股票市场的收益水平产生影响的风险； "),_c('br'),_vm._v("\n        （3）上市公司的经营状况受多种因素影响，如市场、技术、竞争、管理、财务等都可能导致公司盈利发生变化，从而导致股票价格变动的风险。 "),_c('br'),_vm._v("\n        2、债券投资风险主要包括： "),_c('br'),_vm._v("\n        （1）市场平均利率水平变化导致债券价格变化的风险； "),_c('br'),_vm._v("\n        （2）债券市场不同期限、不同类属债券之间的利差变动导致相应期限和类属债券价格变化的风险； "),_c('br'),_vm._v("\n        （3）债券之发行人出现违约、拒绝支付到期本息，或由于债券发行人信用质量降低导致债券价格下降的风险。 "),_c('br'),_vm._v("\n        3、期货投资风险主要包括： "),_c('br'),_vm._v("\n        （1）市场过度投机的风险：期货对交易者的吸引力主要来源于其损益的放大效应，一定程度上，期货工具的引进有可能是相当于又引进了一种投机性更强的工具； "),_c('br'),_vm._v("\n        （2）套利风险：资金管理不当，对期货价格的大幅波动缺乏足够的承受力，当期货价格短期内朝不利方向变动时，此时若没有足够的保证金追加，被迫斩仓，致使保值计划中途夭折； "),_c('br'),_vm._v("\n        （3）监管方面的风险：对期货的监管依据不足，带来期货的交易规则上变数较大，规则的不确定性将蕴藏着巨大的风险，不可避免地遭受行政命令的方式干预市场。 "),_c('br'),_vm._v("\n        （二）信用风险 "),_c('br'),_vm._v("\n        指产品在交易过程中发生交收违约，或者产品所投资债券之发行人出现违约、未能如期足额兑付应付本息，导致产品财产损失。信用风险主要来自于交易对手、发行人和担保人。在产品财产投资运作中，如果产品管理人的信用研究水平不足，对信用产品或交易对手的信用水平判断不准确，可能使产品财产承受信用风险所带来的损失。 "),_c('br'),_vm._v("\n        （三）流动性风险 "),_c('br'),_vm._v("\n        指产品资产不能迅速转变成现金以致不能足额兑付收益的风险。 "),_c('br'),_vm._v("\n        1、市场整体流动性风险 "),_c('br'),_vm._v("\n        证券市场的流动性受到价格、投资群体等诸多因素的影响，在不同状况下，其流动性表现是不均衡的，具体表现为：在某些时期成交活跃，流动性好，而在另一些时期，则可能成交稀少，流动性差。在市场流动性出现问题时，产品的操作有可能发生建仓成本增加或变现困难的情况。 "),_c('br'),_vm._v("\n        2、个别投资品种的流动性风险 "),_c('br'),_vm._v("\n        由于不同投资品种受到市场影响的程度不同，即使在整体市场流动性较好的情况下，一些单一投资品种仍可能出现流动性问题，这种情况的存在使得产品在进行投资操作时，可能难以按预期买入或卖出相应数量的证券，或买入卖出行为对证券价格产生比较大的影响，增加投资成本。这种风险在出现个股和个券停牌、涨跌停板等情况时表现得尤为突出。 "),_c('br'),_vm._v("\n        （四）操作风险 "),_c('br'),_vm._v("\n        1、在产品财产管理运作过程中，对主要业务人员如产品投资经理的依赖可能产生管理风险。可能因产品管理人对经济形势和证券市场等判断有误、获取的信息不完整等影响产品的收益水平。产品管理人和产品托管人的管理水平、管理手段和管理技术等对产品收益水平也存在影响。 "),_c('br'),_vm._v("\n        2、在产品财产管理运作过程中，相关当事人在业务各环节操作过程中，因内部控制存在缺陷或者人为因素，可能造成操作失误或违反操作规程等引致的风险，例如越权违规交易、内幕交易、欺诈、交易错误等。 "),_c('br'),_vm._v("\n        3、在定期开放的产品的各种交易行为或者后台运作中，可能因为技术系统的故障或者差错而影响交易的正常进行或者导致投资者的利益受到影响。这种技术风险可能来自产品管理人、产品托管人、注册登记机构、外包服务机构、销售机构、证券交易所、证券登记结算机构等。 "),_c('br'),_vm._v("\n        4、税务风险：在投资各国或地区市场时，因各国/地区税务法律法规的不同，可能会就股息、利息、资本利得等收益向各国/地区税务机构缴纳税金，包括预扣税，该行为可能会使得资产回报受到一定影响。各国/地区的税收法律法规的规定可能变化，或者加以具有追溯力的修订，所以可能须向该等国家或地区缴纳委托财产销售、估值或者出售投资当日并未预计的额外税项。 "),_c('br'),_vm._v("\n        5、法律及政治管制风险：由于各个国家/地区适用不同法律法规的原因，可能导致产品财产的某些投资行为在部分国家/地区受到限制或合同不能正常执行，或使产品违反有关国家/地区法律法规或产品合同的规定，从而使得产品财产面临损失的可能性。 "),_c('br'),_vm._v(" "),_c('strong',[_vm._v("（五）")]),_c('strong',[_vm._v("产品特定风险")]),_c('strong'),_c('br'),_vm._v(" "),_c('strong',[_vm._v("1")]),_c('strong',[_vm._v("、利率风险 ")]),_c('br'),_vm._v(" "),_c('strong',[_vm._v("金融市场利率的上升和金融行业基本利率的上升都将导致该级份额相对投资收益的降低。2、本金损失风险 ")]),_c('br'),_vm._v(" "),_c('strong',[_vm._v("在")]),_c('strong',[_vm._v("产品")]),_c('strong',[_vm._v("存续期间，如发生风险揭示所述信用风险、市场风险、流动性风险、不可抗力风险或其他风险，则有可能造成")]),_c('strong',[_vm._v("产品")]),_c('strong',[_vm._v("本金及收益的部分或全部损失，即在最不利的投资情况下，持有人本金及收益=0元。 ")]),_c('br'),_vm._v(" "),_c('strong',[_vm._v("3")]),_c('strong',[_vm._v("、预警止损机制的风险 ")]),_c('br'),_vm._v(" "),_c('strong',[_vm._v("虽然")]),_c('strong',[_vm._v("产品")]),_c('strong',[_vm._v("设计有预警止损机制，且")]),_c('strong',[_vm._v("产品")]),_c('strong',[_vm._v("管理人已依照恪尽职守、诚实信用、谨慎勤勉的原则按本合同规定及时执行预警止损，在止损卖出过程中，由于