var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"asurance white-container"},[_c('section',[_vm._v("\n    一、  声明与承诺"),_c('br'),_vm._v("\n    (一) 在接受本协议或您以本公司允许的其他方式实际使用企明星服务之前，请您仔细阅读本协议的全部内容（特别是以粗体标注的内容）。如果您不同意本协议的任意内容，或者无法准确理解本公司对条款的解释，请不要进行后续操作，包括但不限于不要接受本协议，不使用本服务。如果您对本协议的条款有疑问，请通过本公司客服渠道进行询问，本公司将向您解释条款内容。"),_c('br'),_vm._v("\n\n    (二) 您同意，如本公司需要对本协议进行变更或修改的，须通过企明星官方网站或/及官方微信服务号公告的方式提前予以公布，公告期限届满后即时生效；若您在本协议内容公告变更生效后继续使用企明星服务的，表示您已充分阅读、理解并接受变更后的协议内容，也将遵循变更后的协议内容使用企明星服务；若您不同意变更后的协议内容，您应在变更生效前停止使用企明星服务。"),_c('br'),_vm._v("\n\n    (三) 您确认，您在企明星官方网站上发生的所有交易，您已经不可撤销地授权企明星按照其制定的《企明星服务协议》等规则进行处理。本公司按照您的指令进行金融产品的申购、赎回完全来自于您的授权，本公司对因此给您造成的任何损失均不承担责任。但您确认，您使用企明星服务时，您仍应完全遵守本协议及本公司制定的各项规则及页面提示等。"),_c('br'),_vm._v("\n    二、  定义及解释"),_c('br'),_vm._v("\n    (一) 企明星账户（或“该账户”）：指您按照本公司允许的方式取得的供您使用企明星服务的账户。"),_c('br'),_vm._v("\n\n    (二) 本网站：除本协议另有规定外，指企明星相关平台（包括但不限于网站、微信服务号、app）客户端。"),_c('br'),_vm._v("\n    三、  企明星服务"),_c('br'),_vm._v("\n    企明星服务（或称“本服务”）是本公司向您提供的金融理财服务，具体服务项目及内容请见本网站网页，包括（但不限于）以下服务："),_c('br'),_vm._v("\n\n    (一) 申购服务：您可以使用本协议指定的方式申购本公司推荐的理财产品。"),_c('br'),_vm._v("\n\n    (二) 赎回服务：您可以使用本协议指定的方式赎回本公司推荐的理财产品。"),_c('br'),_vm._v("\n\n    (三) 查询服务：本公司将对您委托本公司的所有操作进行记录，您可以在本公司系统中查询您的每日收益、提现记录及转入记录。若您对于记录有异议，本公司可以提供相关申购、赎回及收益纪录。"),_c('br'),_vm._v("\n\n    (四) 增值服务：本公司向您提供其他增值业务，包括以短信或微信的形式向您提示交易进展情况，进行定期收益提醒及下一步的操作提示服务等，您应当及时登录到企明星微信服务号查看并进行相关操作。由于业务需要，本公司有权临时更改增值服务模式而无需事先争得您同意，本公司对于此类增值服务享有最终解释权。"),_c('br'),_vm._v("\n    四、  企明星账户"),_c('br'),_vm._v("\n    (一) 注册相关"),_c('br'),_vm._v("\n\n    除本协议另有规定或相关产品另有规则外，您须在本网站注册并取得本公司提供给您的企明星账户，并且按照本公司要求提供相关信息完成验证后方可使用本服务。您需使用作为企明星登录名的本人手机号码登录企明星账户，并且您应当自行为企明星账户设置密码。您同意："),_c('br'),_vm._v("\n\n    1.  按照本公司要求准确提供并在取得企明星账户后及时更新您正确、最新及完整的公司信息及相关资料。"),_c('br'),_vm._v("\n\n    2.  您应及时更新您的公司信息及相关资料（包括但不限于企业名称、银行账号、开户行信息、作为企明星登录名的手机号码、与企明星账户绑定的手机号码等），否则企明星有权将企明星登录名、企明星账户绑定的手机号码开放给其他用户注册或使用。因您未及时更新资料导致的一切后果，均应由您自行承担，该后果包括但不限于导致赎回时发生任何错误、企明星账户及该账户内余额被别人盗用，且您不得将此作为取消操作的理由。"),_c('br'),_vm._v("\n\n    (二) 账户安全"),_c('br'),_vm._v("\n\n    您将对使用企明星账户或密码、校验码进行的一切操作及言论负完全的责任，您同意："),_c('br'),_vm._v("\n\n    1.  除相关产品另有规则外，本公司可以通过您的企明星登录名和密码或者本公司认可的其他方式识别您的身份，您应当对该等企明星登录名、密码、校验码、身份识别信息等进行妥善保管，对于因企明星登录名、密码、校验码、身份识别信息等泄露所致的损失由您自行承担。您保证不向 其他任何人泄露您的企明星登录名、密码、校验码以及身份信息等，亦不使用其他任何人的企明星登录名、密码、校验码、身份识别信息等。"),_c('br'),_vm._v("\n\n    2.  您同意，（a）如您发现有他人冒用或盗用您的企明星登录名及密码或任何其他未经合法授权之情形，您应立即以有效方式通知本公司，向本公司申请暂停相关企明星服务；及（b）确保您在持续登录网站时段结束时，以正确步骤离开网站。本公司不能也不会对因您未能遵守本款约定而发生 的任何损失、损毁及其他不利后果负责。"),_c('br'),_vm._v("\n\n    3.  您确认，您应自行对您的企明星账户负责，只有您本人方可使用该账户。该账户不可转让、不可赠与、不可继承，但账户内的相关财产权益可被依法继承。"),_c('br'),_vm._v("\n\n    4.  您同意，基于运行和交易安全的需要，本公司可以暂时停止提供或者限制本服务部分功能，或提供新的功能，在任何功能减少、增加或者变化时，只要您仍然使用本服务，表示您仍然同意本协议或者变更后的协议。"),_c('br'),_vm._v("\n\n    5.  您同意，本公司有权按照国家法律或行政法规所赋予权力的有权机关的要求提供您的公司信息或对您的企明星账户进行查询、冻结或扣划。"),_c('br'),_vm._v("\n\n    五、  企明星服务使用规则"),_c('br'),_vm._v("\n    为有效保障您使用本服务时的合法权益，您理解并同意接受以下规则："),_c('br'),_vm._v("\n\n    (一) 一旦您使用本服务，您即不可撤销地授权本公司在您符合指定条件或状态时，采取申购/赎回操作。"),_c('br'),_vm._v("\n\n    (二) 本公司通过以下方式接受来自您的指令：A、您在本网站或微信服务号上通过以您的企明星登陆名及密码登录企明星账户并依照本服务预设流程所确认的交易状态或指令；B、本公司与您约定或本公司认可的其他方式。无论您通过以上何种方式向本公司发出指令，都不可撤回或撤销，且成为本公司代理您申购/赎回或进行其他操作的唯一指令，视为您本人的指令，您应当自己对本公司忠实执行上述指令产生的任何结果承担责任。"),_c('br'),_vm._v("\n\n    (三) 您在使用本服务过程中，本协议内容、网页与微信服务号上出现的关于操作的提示或本公司发送到该手机的信息 （短信或电话等）内容是您使用本服务的相关规则，您使用本服务即表示您同意接受本服务的相关规则。本服务的相关规则应以您使用服务时的页面提示（或发送到该手机的短信或电话或微信服务号通知等）为准，您同意并遵照本服务的相关规则是您使用本服务的前提。"),_c('br'),_vm._v("\n\n    (四) 本公司会以网站公告、微信服务号通知、发送到该手机的短信或电话通知等方式向您发送通知，例如通知您交易进展情况，或者提示您进行相关操作，您应及时予以关注。但本公司不保证您能够收到或者及时收到该等通知，且对此不承担任何后果。因您没未能提交相关申请而导致的任何纠 纷或损失，本公司不负任何责任。"),_c('br'),_vm._v("\n\n    (五) 您理解，基于相关法律法规，对本协议项下的任何服务，本公司均不提供任何担保。"),_c('br'),_vm._v("\n\n    (六) 您不得将本服务用于非本公司许可的其他用途。"),_c('br'),_vm._v("\n\n    (七) 交易风险"),_c('br'),_vm._v("\n\n    1.  在使用本服务时，若您未遵从本协议或相关网站说明、交易、支付页面中之操作提示、规则，则本公司有权拒绝为您提供相关服务，且本公司不承担损害赔偿责任。"),_c('br'),_vm._v("\n\n    2.  因您的过错或过失导致的任何损失由您自行承担，该过错或过失包括但不限于：不按照提示操作，未及时进行交易操作，遗忘或泄漏密码、校验码等，密码被他人破解，您使用的计算机或其他硬件、终端等被他人使用、被他人侵入或丢失，或您使用的软件被他人侵入。"),_c('br'),_vm._v("\n\n    3.  您使用本服务时同意并认可，可能由于银行本身系统问题、银行相关作业网络连线问题、任何互联网系统问题或网络运营商原因、您的手机通讯系统问题或通讯服务商原因或其他不可抗拒因素，造成本服务无法提供，对此本公司不承担任何责任。"),_c('br'),_vm._v("\n\n    (八) 服务费用"),_c('br'),_vm._v("\n\n    在您使用本服务时，除非另有说明或约定，您同意本公司有权自您委托本公司理财的本金与收益中直接扣除上述服务费用。"),_c('br'),_vm._v("\n\n    (九) 您认可企明星账户的使用记录、交易金额数据等均以本公司系统记录的数据为准。如您对该等数据存有异议的，应自您账户数据发生变动之日起三日内向本公司提出异议，并提供相关证据供本公司核实，否则视为您认可该数据。"),_c('br'),_vm._v("\n    六、  企明星服务使用限制"),_c('br'),_vm._v("\n    (一) 您在使用本服务时应遵守中华人民共和国相关法律法规及您所属、所居住或开展经营活动或其他业务的国家或地区的法律法规，不得将本服务用于任何非法目的（包括用于禁止或限制交易物品的交易），也不得以任何非法方式使用本服务。"),_c('br'),_vm._v("\n\n    (二) 您不得利用本服务从事侵害他人合法权益之行为， 否则本公司有权拒绝提供本服务，且您应承担所有相关法律责任，因此导致本公司或本公司雇员或其他方受损的，您应承担赔偿责任。"),_c('br'),_vm._v("\n\n    上述（一）和（二）适用的情况包括但不限于："),_c('br'),_vm._v("\n\n    1.  侵害他人名誉权、隐私权、商业秘密、商标权、著作权、专利权等合法权益。"),_c('br'),_vm._v("\n\n    2.  违反依法定或约定之保密义务。"),_c('br'),_vm._v("\n\n    3.  冒用他人名义使用本服务。"),_c('br'),_vm._v("\n\n    4.  提供赌博资讯或以任何方式引诱他人参与赌博。"),_c('br'),_vm._v("\n\n    5.  非法使用他人银行账户（包括信用卡账户）或无效银行账户（包括信用卡账户）交易。"),_c('br'),_vm._v("\n\n    6.  从事任何可能含有电脑病毒或是可能侵害本服务系统、资料之行为。"),_c('br'),_vm._v("\n\n    7.  其他本公司有正当理由认为不适当之行为。"),_c('br'),_vm._v("\n\n    (三) 您理解并同意，本公司不对因下述任一情况导致的任何损害赔偿承担责任，包括但不限于利润、商誉、使用、数据等方面的损失或其他无形损失的损害赔偿（无论本公司是否已被告知该等损害赔偿的可能性）："),_c('br'),_vm._v("\n\n    1.  本公司有权基于单方判断，包含但不限于本公司认为您已经违反本协议的明文规定及精神，对您名下的全部或部分企明星账户暂停、中断或终止向您提供本服务或其任何部分，并移除您的资料。"),_c('br'),_vm._v("\n\n    2.  您理解并同意，存在如下情形时，本公司有权对您名下企明星账户暂停或终止提供全部或部分企明星服务，并通过短信或微信等方式通知您，您应及时予以关注："),_c('br'),_vm._v("\n\n    1)  根据本协议的约定；"),_c('br'),_vm._v("\n\n    2)  根据法律法规及法律文书的规定；"),_c('br'),_vm._v("\n\n    3)  根据有权机关的要求；"),_c('br'),_vm._v("\n\n    4)  您使用企明星服务的行为涉嫌违反国家法律法规及行政规定的；"),_c('br'),_vm._v("\n\n    5)  本公司基于单方面合理判断认为该账户操作、资金进出等存在异常时；"),_c('br'),_vm._v("\n\n    6)  本公司依据自行合理判断认为可能产生风险的；"),_c('br'),_vm._v("\n\n    7)  如您申请恢复服务、解除上述限制，您应按本公司要求如实提供相关资料及您的身份证明以及本公司要求的其他信息或文件，以便本公司进行核实，且本公司有权依照自行判断来决定是否同意您的申请。您应充分理解您的申请并不必然被允许。您拒绝如实提供相关资料及身份证明的，或未通过 本公司审核的，则您确认本公司有权长期对该等账户停止提供服务且长期限制该等产品或者服务的部分或全部功能。