import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popover from 'vux/src/components/popover/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
  data () {
    return {
        title: '',
        dialogMsg: '',
        dialogLink: '',
        dialogVisible: false,
        clicked_01: false,
        clicked_02: false,
        product: {
            jiaxiquan: {
                jiaxi: 0,
            },
            quanminjiaxi: {
                jiaxi: 0,
            }
        }
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
    XHeader,
    Cell,
    Group,
    Popover,
    Flexbox,
    FlexboxItem,
    XButton,
    XDialog
  },
  created () {
      document.title = '企明星活期'
      this.$nextTick(() => {
          this.getAssetsDetail()
      })
  },
  methods: {
      jump(path,query){
          this.$store.dispatch('getUserStatus').then((res) => {
              this.$router.push({path: path, query: query})
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      getAssetsDetail () {
          this.$store.dispatch('getAssetsDetail', {'bd_code': this.$route.query.bd_code, 'asset_type': 'hq'}).then((res) => {
              this.product = res
              this.title = res.goods_name
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      }
  }
}



