//
//

import XHeader from 'vux/src/components/x-header/index.vue'
import loginImg from '../../assets/images/login-logo.png'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      loginImg: loginImg,
      showToast: false,
      user_name: '',
      user_pwd: '',
      img_code: '',
      codeImg: '',
      sid: '',
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
      domain: ''
    }
  },
  directives: {
    TransferDom
  },
  components: {
    XHeader,
    Group,
    XInput,
    XButton,
    Toast,
    TransferDom,
    XDialog
  },

    created () {
        this.$nextTick(()=>{
            this.passport_logout ()
        })
    },
  methods: {
      passport_logout () {
          this.$store.dispatch('passport_logout').then((res) => {
              this.$router.push('/')
          }).catch((res) => {
          })
      }
  },
}



