import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
export default {
  data () {
    return {
      title: '',
      showDialog: false,
      showConfirm: false,
        errMsg:'',
       dialogMsg: '',
        result: {},
        sn: '',
        use_tags: '',
        other_tags: '',
        accountValue: '余额账户',
        accountKey: 'BASIC',
        openSelectAccount: false,

        redeemUsage: '待选择',
        openSelectUse: false,
        otherUse:false,
        listUse: [],


    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
    TransferDom
  },
  components: {
    Cell,
    Group,
    XInput,
    XButton,
    XDialog,
    Confirm,
      XHeader,
      Selector
  },
  created () {
      document.title = '赎回银行票据'
      this.$nextTick(() => {
          this.bao_bill_redeem_info()
      })
  },
  methods: {
      selectUseConfirm () {
          for (let x in this.listUse){
              if(this.listUse[x].selected == true){
                  this.redeemUsage = '已选择',
                      this.openSelectUse = false
              }
          }
          if(this.otherUse){
              this.redeemUsage = '已选择',
                  this.openSelectUse = false
          }
      },
      selectUseCancel () {
          for (let x in this.listUse){
              this.listUse[x].selected = false
              this.openSelectUse = false
              this.redeemUsage = '待选择'
          }
          this.otherUse = false
      },
      getUseTags () {
          let use = ''
          for (let x in this.listUse){
              if(this.listUse[x].key === ''){
                  continue
              }
              if(this.listUse[x].selected === true){
                  if (use != "") use += ",";
                  use +=this.listUse[x].key
              }
          }
          return use
      },
      bao_bill_redeem_info () {
          this.$store.dispatch('bao_bill_redeem_info', {sn: this.$route.query.sn}).then((res) => {
              this.result = res
              this.sn = res.out_trade_no
              this.listUse = res.tags
          }).catch((res) => {
              if(res.errcode==40011){
                  this.dialogMsg = res.errmsg
                  this.showDialog = true
              }
          })
      },
      onConfirm () {
          let use_tags = this.getUseTags();
          if(use_tags === ''&& this.other_tags=== ''){
              this.dialogMsg = '请选择或填写用途'
              this.showDialog = true
              return
          }
        let form = new FormData()
        form.append('sn', this.sn)
        form.append('payee_type', this.accountKey)
        form.append('use_tags', use_tags)
        form.append('other_tags', this.other_tags)
        this.$store.dispatch('bao_ajaxBillPreengageRedeem', form).then((res) => {
            this.$router.push({name:'redeemReturn', query:{sn: this.sn, bill:1}})
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.showDialog = true
        })
    },
  }
}



