import Vue from 'vue'
import Router from 'vue-router'
// account
import account from '@/components/account/Account'
import accountHome from '@/components/account/Home'
import accountAssets from '@/components/account/Assets'
import accountHq from '@/components/account/AccountHq'
import accountDq from '@/components/account/AccountDq'
import accountBill from '@/components/account/AccountBill'
import accountBank from '@/components/account/AccountBank'
import accountSavePot from '@/components/account/AccountSavePot'
import transactionDetail from '@/components/account/TransactionDetail'
import profitDetail from '@/components/account/ProfitDetail'
import redeemConfig from '@/components/account/RedeemConfig'
import buy from '@/components/account/Buy'
import redeemHq from '@/components/account/RedeemHq'
import redeemDq from '@/components/account/RedeemDq'
import redeemBill from '@/components/account/RedeemBill'
import rechargeSuccess from '@/components/account/RechargeSuccess'
import orderDetail from '@/components/account/orderDetail'
import cash from '@/components/account/Cash'
import redeemReturn from '@/components/account/redeemReturn'
import sinaReturn from '@/components/account/sinaReturn'
import sinaReturnDetail from '@/components/account/sinaReturnDetail'
import commonReturn from '@/components/account/commonReturn'

// auth
import login from '@/components/auth/Login'
import logout from '@/components/auth/logout'
import register from '@/components/auth/Register'
import register1 from '@/components/auth/Register1'
import register2 from '@/components/auth/Register2'
import forgotPwd from '@/components/auth/ForgotPwd'
import asurance from '@/components/auth/Asurance'
import qa from '@/components/auth/QA'
import contract from '@/components/auth/Contract'
// home
import home from '@/components/home/Home'
import index from '@/components/home/Index'
import product from '@/components/home/product'
import productMuji from '@/components/home/productMuji'
import customProduct from '@/components/home/CustomProduct'
import homeHq from '@/components/home/HomeHq'
import homeDq from '@/components/home/HomeDq'
import homeBillList from '@/components/home/HomeBillList'
import activeproduct from '@/components/home/activeproduct'
import lowproduct from '@/components/home/lowproduct'
import homeBill from '@/components/home/HomeBill'
import homeNewSavePot from '@/components/home/HomeNewSavePot'
import homeSavePot from '@/components/home/HomeSavePot'
import homeBank from '@/components/home/HomeBank'
import reBack from '@/components/account/Reback'
import rebackDetail from '@/components/account/RebackDetail'
import made from '@/components/home/Made'
import dollar from '@/components/home/Dollar'
// xieyi
import xieyiRegister from '@/components/xieyi/register'
import hqhetong from '@/components/xieyi/hqhetong'
import fengxiangaozhi from '@/components/xieyi/fengxiangaozhi'
import dingqixieyi from '@/components/xieyi/dingqixieyi'
import billxieyi from '@/components/xieyi/billxieyi'
import serveprotocol from '@/components/xieyi/serveprotocol'
import riskannouncement from '@/components/xieyi/riskannouncement'
import rengouxieyi from '@/components/xieyi/rengouxieyi'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/account',
      name: 'account',
      component: account,
      children: [
    		{
    		  path: 'home',
    		  name: 'account-home',
    		  component: accountHome,
    		  meta: {
    		    title: ''
    		  }
    		},
        {
          path: 'assets',
          name: 'account-assets',
          component: accountAssets,
          meta: {
            title: '资产概况',
            keepAlive: true
          }
        },
        {
            path: 'reback',
            name: 'reback',
            component: reBack,
            meta: {
                title: '已到期 / 赎回'
            }
        },
        {
            path: 'rebackDetail',
            name: 'rebackDetail',
            component: rebackDetail,
            meta: {
                title: '已到期 / 赎回详情'
            }
        },
        {
          path: 'hq',
          name: 'accountHq',
          component: accountHq,
          meta: {
            title: '企明星活期'
          }
        },
        {
          path: 'dq',
          name: 'accountDq',
          component: accountDq,
          meta: {
            title: '企明星定期'
          }
        },
        {
          path: 'bill',
          name: 'accountBill',
          component: accountBill,
          meta: {
            title: '电子银行票据理财'
          }
        },
        {
          path: 'bank',
          name: 'accountBank',
          component: accountBank,
          meta: {
            title: '银行理财产品'
          }
        },
        {
          path: 'save_pot',
          name: 'accountSavePot',
          component: accountSavePot,
          meta: {
            title: '汇添富现金宝货币基金'
          }
        },
        {
          path: 'transaction-detail',
          name: 'transaction-detail',
          component: transactionDetail,
          meta: {
            title: '交易明细',
            keepAlive: true
          }
        },
        {
          path: 'profitDetail',
          name: 'profitDetail',
          component: profitDetail,
          meta: {
            title: '收益明细'
          }
        },
        {
          path: 'redeem-config',
          name: 'redeem-config',
          component: redeemConfig,
          meta: {
            title: '到期赎回设置'
          }
        },
        {
          path: 'buy',
          name: 'buy',
          component: buy,
          meta: {
            title: '购买'
          }
        },
        {
          path: 'redeem_hq',
          name: 'redeem_hq',
          component: redeemHq,
          meta: {
            title: '赎回活期'
          }
        },
        {
          path: 'redeem_dq',
          name: 'redeem_dq',
          component: redeemDq,
          meta: {
            title: '赎回定期'
          }
        },
          {
              path: 'redeem_bill',
              name: 'redeem_bill',
              component: redeemBill,
              meta: {
                  title: '赎回银行票据'
              }
          },
        {
          path: 'recharge-success',
          name: 'recharge-success',
          component: rechargeSuccess,
          meta: {
            title: '购买'
          }
        },
        {
              path: 'orderDetail',
              name: 'orderDetail',
              component: orderDetail,
              meta: {
                  title: ''
              }
          },
        {
          path: 'cash',
          name: 'cash',
          component: cash,
          meta: {
            title: '提现'
          }
        }
      ]
    },
      {
          path: '/redeemReturn',
          name: 'redeemReturn',
          component: redeemReturn
      },
      {
          path: '/sinaReturn',
          name: 'sinaReturn',
          component: sinaReturn
      },
      {
          path: '/sinaReturnDetail',
          name: 'sinaReturnDetail',
          component: sinaReturnDetail
      },
      {
          path: '/commonReturn',
          name: 'commonReturn',
          component: commonReturn
      },
    {
      path: '/login',
      name: 'login',
      component: login
    },
      {
          path: '/logout',
          name: 'logout',
          component: logout
      },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/register1',
      name: 'register1',
      component: register1
    },
    {
      path: '/register2',
      name: 'register2',
      component: register2
    },
    {
      path: '/forgot-pwd',
      name: 'forgot-pwd',
      component: forgotPwd
    },
    {
      path: '/asurance',
      name: 'asurance',
      component: asurance
    },
    {
      path: '/qa',
      name: 'qa',
      component: qa
    },
    {
      path: '/contract',
      name: 'contract',
      component: contract
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children: [
        {
          path: 'index',
          name: 'index',
          component: index,
          meta: {
            title: ''
          }
        },
        {
          path: 'product',
          name: 'product',
          component: product,
          meta: {
            title: ''
          }
        },
        {
          path: 'productMuji',
          name: 'productMuji',
          component: productMuji,
          meta: {
            title: '',
            showFooter: true
          }
        },
        {
          path: 'custom-product',
          name: 'custom-product',
          component: customProduct,
          meta: {
            title: '定制产品',
            showFooter: true
          }
        },
        {
          path: 'bill_list',
          name: 'bill_list',
          component: homeBillList,
          meta: {
            title: '',
            showFooter: true
          }
        },
        {
              path: 'activeproduct',
              name: 'activeproduct',
              component: activeproduct,
              meta: {
                  title: '',
                  showFooter: true
              }
          },
          {
              path: 'lowproduct',
              name: 'lowproduct',
              component: lowproduct,
              meta: {
                  title: '',
                  showFooter: true
              }
          },
        {
          path: 'hq',
          name: 'homeHq',
          component: homeHq,
          meta: {
            title: '企明星活期'
          }
        },
          {
              path: 'dq',
              name: 'homeDq',
              component: homeDq,
              meta: {
                  title: '企明星定期'
              }
          },
        {
          path: 'bill',
          name: 'homeBill',
          component: homeBill,
          meta: {
            title: '电子银行票据'
          }
        },
        {
          path: 'new_save_pot',
          name: 'homeNewSavePot',
          component: homeNewSavePot,
          meta: {
            title: '新客户首投专享产品'
          }
        },
        {
          path: 'save_pot',
          name: 'homeSavePot',
          component: homeSavePot,
          meta: {
            title: '汇添富现金宝货币基金'
          }
        },
        {
            path: 'bank',
            name: 'homeBank',
            component: homeBank,
            meta: {
                title: '银行理财产品'
            }
        },
          {
              path: 'dollar',
              name: 'dollar',
              component: dollar,
              meta: {
                  title: '美金现金管理产品',
                  showFooter: true
              }
          },
          {
              path: 'made',
              name: 'made',
              component: made,
              meta: {
                  title: '企明星定制理财'
              }
          }
      ]
    },
    {
          path: '/xieyiRegister',
          name: 'xieyiRegister',
          component: xieyiRegister
      },
      {
          path: '/hqhetong',
          name: 'hqhetong',
          component: hqhetong
      },
      {
          path: '/fengxiangaozhi',
          name: 'fengxiangaozhi',
          component: fengxiangaozhi
      },
      {
          path: '/serveprotocol',
          name: 'serveprotocol',
          component: serveprotocol
      },
      {
          path: '/riskannouncement',
          name: 'riskannouncement',
          component: riskannouncement
      },
      {
          path: '/rengouxieyi',
          name: 'rengouxieyi',
          component: rengouxieyi
      },
      {
          path: '/dingqixieyi',
          name: 'dingqixieyi',
          component: dingqixieyi
      },
      {
          path: '/billxieyi',
          name: 'billxieyi',
          component: billxieyi
      },
    {
      path: '/',
      redirect: '/home/index'
    }
  ]
})



