import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import navMenu from '@/components/home/Nav'
import logoImg from '../../assets/images/logo.png'
export default {
  data () {
    return {
        title: '',
       logoImg: logoImg,
        showNav: false,
      showDialog: false,
      showConfirm: false,
        inlink: true,
        dialogMsg: '',
        
        dialogLink: '',
        confirmMsg: '',
        shuhui:[],
        currentMenu: 1,
        redeem: '',
        typeKey: 1,
        openSelectType: 1,

        confirmVisible: false,
        confirmStatus:false,
        accountKey: 'BASIC',
        openSelectAccount: 'BASIC',


        redeemUsage: '待选择',
        openSelectUse: false,
        otherUse:false,
//        listUse: [{key: 1, value: '工资及社保',selected: false}, {key: 2, value: '行政办公费',selected: false}, {key: 3, value: '房租及物业费',selected: false}, {key: 4, value: '公司业务',selected: false}, {key: 5, value: '员工福利',selected: false}, {key: 6, value: '财务审计',selected: false}, {key: 7, value: '投资',selected: false}],
        listUse:[],
        goods_model:'',
        amount:'',
        daozhang_time: '',
        use_tags: '',
        other_tags: '',
        date: '',

        red: false
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
    TransferDom
  },
  components: {
    Cell,
    Group,
    XInput,
    XButton,
    XDialog,
    Confirm,
      XHeader,
    Selector,
    Datetime,
      navMenu
  },
    watch: {
        amount(newAmount, oldAmount) {
            this.checkShuhuiMoney()
        }
    },
    created () {
        document.title = '赎回活期'
        this.$nextTick(() => {
            this.bao_redeem_info()
        })
    },
  methods: {
      onConfirm(){
         this.confirmStatus = true
          this.submit()
      },
      selectUseConfirm () {
          for (let x in this.listUse){
              if(this.listUse[x].selected == true){
                  this.redeemUsage = '已选择',
                  this.openSelectUse = false
              }
          }
          if(this.otherUse){
              this.redeemUsage = '已选择',
              this.openSelectUse = false
          }
      },
      selectUseCancel () {
          this.openSelectUse = false
          for (let x in this.listUse){
              this.listUse[x].selected = false
              this.redeemUsage = '待选择'
          }
          this.otherUse = false
      },
      getUseTags () {
          let use = ''
          for (let x in this.listUse){
              if(this.listUse[x].selected === true){
                  if(this.listUse[x].key === ''){
                      continue
                  }
                  if (use != "") use += ",";
                  use +=this.listUse[x].key
              }
          }
          return use
      },
      bao_redeem_info () {
          this.$store.dispatch('bao_redeem_info', {'bd_code': this.$route.query.bd_code}).then((res) => {
              this.shuhui = res
              this.goods_model = res.goods_model
              this.daozhang_time = res.daozhang_date
              this.title = res.product_name,
              this.listUse =res.tags
          }).catch((res) => {
              if(res.errcode === 40001){
                  this.$router.push('/login')
              }
              if(res.errcode === 40011){
                  this.dialogMsg = res.errmsg
                  this.showDialog = true
              }
          })
      },
      checkShuhuiMoney (){
          let day = 1
          if (parseFloat(this.amount) > parseFloat(this.shuhui.msg.draw_money_max)) {
              day = 5;
              this.daozhang_time = this.shuhui.daer_date
              this.red = true
//              this.dialogMsg = '累计赎回金额已超过'+this.shuhui.yanchidaozhang_money/10000+ '万，预计'+this.shuhui.yanchidaozhang_info+ '到账'
          } else {
              this.red = false
              this.daozhang_time = this.shuhui.daozhang_date
//              this.dialogMsg = '预计'+this.shuhui.msg.date+'到账'
          }
      },

    submit () {
          this.checkShuhuiMoney()

        if(this.amount == ''){
            this.dialogMsg = '金额不能为空'
            this.showDialog = true
            return
        }
        if(parseFloat(this.amount) > parseFloat(this.shuhui.my_suisui_balance)){
            this.dialogMsg = '赎回金额过大'
            this.showDialog = true
            return
        }
        if(this.typeKey==2&&this.date==''){
            this.dialogMsg = '请选择预约时间'
            this.showDialog = true
            return
        }
        let use_tags = this.getUseTags()

        if(use_tags === ''&& this.other_tags=== ''){
            this.dialogMsg = '请选择或填写用途'
            this.showDialog = true
            return
        }
        if(!this.confirmStatus){
            this.confirmVisible = true
            return
        }
        let form = new FormData()
        form.append('redeem_type', this.typeKey)
        form.append('payee_type', this.accountKey)
        form.append('amount', this.amount)
        form.append('use_tags', use_tags)
        form.append('other_tags', this.other_tags)
        form.append('daozhang_time', this.date)
        form.append('bd_code', this.$route.query.bd_code)
     if (this.goods_model === 1){
         this.$store.dispatch('savingpot_check_shuhui', form).then((res) => {
             this.dialogMsg = '请在新浪收银台完成操作。如果遇到问题，请致电客服：400-892-6600'
             this.showDialog = true
             this.inlink = false
             this.dialogLink = res
         }).catch((res) => {
             this.dialogMsg = res.errmsg
             this.showDialog = true
         })
     } else {
         let check = 'bao_check_shuhui'
         let shuhuiReturn = 'bao_shuhui_return'
         if(this.typeKey == 2){
             check = 'bao_yuyue_check_shuhui';
             shuhuiReturn = 'bao_yuyue_shuhui_return'
         }
         this.$store.dispatch(check, form).then((res) => {

             this.$store.dispatch(shuhuiReturn, form).then((res) => {
                 this.$router.push({name:'redeemReturn', query:{yuyue:res.yuyue, order_no: res.order_no}})
             }).catch((res) => {
                 this.dialogMsg = res.errmsg
                 this.showDialog = true
             })
         }).catch((res) => {
             this.dialogMsg = res.errmsg
             this.showDialog = true
         })
     }
    },

    ok () {
      this.showDialog = false
    }
  }
}



