import {app} from '../main'

// const configPath = process.env.NODE_ENV === 'production' ? (process.env.API_ROOT + '/api/app') : '/api/app'

export class Utils {
  get (url, data = {}, isPC) {
    url = this.configPath(url, isPC)
    app.$store.commit('SET_LOADING', true)
    return new Promise((resolve, reject) => {
      app.$http.get(url, {params: data}).then((response) => {
        resolve(response.body)
        app.$store.commit('SET_LOADING', false)
      }).catch((response) => {
        console.log(response)
        app.$store.commit('SET_LOADING', false)
        if (response.status === 502 || response.status === 400 || response.status === 500) {
          response.statusText = '发生错误，请稍后重试'
          reject(response.statusText)
        } else {
          reject(response.body.errmsg)
        }
      })
    })
  }

  post (url, body = {}, isPC) {
    url = this.configPath(url, isPC)
    app.$store.commit('SET_LOADING', true)
    return new Promise((resolve, reject) => {
      app.$http.post(url, body, {headers: 'Content-Type: multipart/form-data'}).then((response) => {
        resolve(response.body)
        app.$store.commit('SET_LOADING', false)
      }).catch((response) => {
        console.log(response)
        app.$store.commit('SET_LOADING', false)
        if (response.status === 502 || response.status === 400 || response.status === 500) {
          response.statusText = '发生错误，请稍后重试'
          reject(response.statusText)
        } else {
          reject(response.body.errmsg)
        }
      })
    })
  }

  configPath (url, isPC) {
    if (process.env.NODE_ENV === 'production') {
      if (isPC) {
        url = process.env.API_ROOT_PC + url
      } else {
          if (process.env.type === 'test') {
              url = process.env.API_ROOT_TEST + url
          } else {
              url = process.env.API_ROOT + url
          }
      }
    } else {
      if (isPC) {
        url = '/api/pc' + url
      } else {
        url = '/api/app' + url
      }
    }
    return url
  }

  // ex大于1000表示设置毫秒，否则设置天数
  setCookie (cname, cvalue, ex) {
    let d = new Date()
    if (ex < 1000) {
      d.setTime(d.getTime() + (ex * 24 * 60 * 60 * 1000))
    } else {
      d.setTime(d.getTime() + ex)
    }
    let expires = 'expires=' + d.toUTCString()
    document.cookie = cname + '=' + cvalue + '; ' + expires
  }

  getCookie (cname) {
    var name = cname + '='
    var ca = document.cookie.split(';')
    for (var i = 0; i < ca.length; i++) {
      var c = ca[i]
      while (c.charAt(0) === ' ') c = c.substring(1)
      if (c.indexOf(name) !== -1) {
        let rs = c.substring(name.length, c.length)
        if (rs && rs !== 'undefined') { return rs }
      }
    }
    return false
  }

  clearCookie (name) {
    this.setCookie(name, '', -1)
  }
}



