import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popover from 'vux/src/components/popover/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      title: '',
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
      clicked_01: false,
      clicked_02: false,
      product: {
          jiaxiquan: {
              jiaxi: 0
          }
      }
    }
  },
  directives: {
      TransferDom
  },
  computed: {
    ...mapGetters(['company'])
  },
  components: {
    XHeader,
    Cell,
    Group,
    Flexbox,
    FlexboxItem,
    XButton,
    Popover,
    XDialog
  },
  created () {
      document.title = '企明星定期'
      this.$nextTick(() => {
          this.getAssetsDetail()
      })
  },
  methods: {
      jump(path,query){
          this.$store.dispatch('getUserStatus').then((res) => {
              this.$router.push({path: path, query: query})
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      getAssetsDetail () {
          this.$store.dispatch('getAssetsDetail', {'bd_code': this.$route.query.bd_code, 'out_trade_no': this.$route.query.outTradeNo, 'asset_type': 'dq'}).then((res) => {
              this.product = res
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      }
  }
}



