import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import statusBar from '../home/statusBar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
export default {
    data () {
        return {
            title: '',
            dialogShow: false,
            dialogMsg: '',
            dialogLink: '',
            order_type: '',
            button_msg: '返回企明星账户',
            step: 0,
            content_all: {
                desc1: '',
                time1: '',
                desc2: '',
                time2: ''
            }
        }
    },
    computed: {
        ...mapGetters(['company'])
    },
    directives: {
        TransferDom
    },
    components: {
        Group,
        Flexbox,
        FlexboxItem,
        XButton,
        statusBar,
    },
    created () {
        document.title = '中子星金融'
        this.$nextTick(() => {
            this.getDetail()
        })
    },
    methods: {
        getDetail () {
            this.$store.dispatch('sinapay_index', {'trade_type':this.$route.query.trade_type,'trade_no':this.$route.query.trade_no,'sign': this.$route.query.sign}).then((res) => {
                this.title = res.msg
                this.content_all.desc1 = res.begin_msg
                this.content_all.desc2 = res.end_msg
                this.content_all.time1 = res.create_time
                    if(res.order_status === 'done'){
                    this.step = 2
                    this.content_all.time2 = res.update_time
                    }
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogShow = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                }
            })
        }
    }
}



