import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
export default {
  data () {
    return {
      title: '',
      agree: true,
      showDialog: false,
        dialogMsg: '',
        dialogLink: '',
        errMsg:'',
      buyInfo: {},
        amount: '',
        bd_code: this.$route.query.bd_code,
        sg_code: this.$route.query.sg_code,
        goods_model: '',
        zhuanrang_dingqi: '',
        inlink: true,
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
    TransferDom
  },
  components: {
    Cell,
    Group,
    XInput,
    XButton,
    CheckIcon,
    TransferDom,
    XDialog,
    XHeader
  },
  created () {
      document.title = '购买'
      this.$nextTick(() => {
          this.bao_buy_info()
      })
  },
  methods: {
     bao_buy_info () {
          this.$store.dispatch('bao_buy_info', {'bd_code': this.$route.query.bd_code, 'sg_code': this.$route.query.sg_code, 'code': this.$route.query.code},).then((res) => {
              this.buyInfo = res
              this.title = '购买'+res.goods_name
              this.goods_model = res.goods_model
              if (this.goods_model === 6) {
                  this.amount = res.dingtou
              }
              if (res.goods_type === 0) {
                  this.zhuanrang_dingqi = 'hqhetong'
              } else {
                  this.zhuanrang_dingqi = 'dingqixieyi'
              }
              if(res.unpayOrder){
                  this.dialogMsg = '有未支付订单'
                  this.dialogLink = '/'
                  this.showDialog = true
              }
          }).catch((res) => {
              if(res.errcode === 40001){
                  this.dialogLink='/login'
              }
              this.dialogMsg = res.errmsg
              this.showDialog = true
          })
      },
      create(){},
    submit () {
        this.errMsg = ''
         let amount = this.amount

         if (amount === '') {
             this.dialogMsg = '请输入正确的金额'
             this.showDialog = true
             return
         }
        if (amount < this.buyInfo.qitou) {
            this.dialogMsg = '购买金额不能小于最低可购买金额'
            this.showDialog = true
            return
        }
        if (amount > this.buyInfo.ketou*10000) {
            this.dialogMsg = '购买金额不能大于本次可购买金额'
            this.showDialog = true
            return
        }
        if (this.agree != true) {
            this.dialogMsg = '请阅读相关协议'
            this.showDialog = true
            return
        }
        let form = new FormData()
        form.append('bd_code', this.$route.query.bd_code)
        form.append('sg_code', this.$route.query.sg_code)
        form.append('amount', this.amount)
        form.append('check_type', 'zhuanru')
        this.$store.dispatch('bao_create_trade', form).then((res) => {
//            form.delete('check_type')
            this.dialogMsg = '请在新浪收银台完成操作。如果遇到问题，请致电客服：400-892-6600'
            this.dialogLink = res
            this.inlink = false
            this.showDialog = true
        }).catch((res) => {
             if(res.errcode==40011){
                 this.dialogMsg = res.errmsg
                 this.showDialog = true
                 this.dialogLink = ''
             }
            if(res.errcode!=40011){
                this.dialogMsg = res.errmsg
                this.showDialog = true
                this.dialogLink = '/'
            }

        })
    }
  }
}



