import {Utils} from '../../store/util'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
export default {
  data () {
    return {
      showNav: false,
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
      examineVisible: true,
      company: {}
    }
  },
  created () {
      document.title = '我的账户'
      this.$nextTick(() => {
          this.bao_profile()
      })
  },
//  activated () {
//      this.$nextTick(() => {
//          this.bao_profile()
//      })
//  },
    directives: {
        TransferDom
    },
  components: {
    Group,
    Cell,
    Grid,
    GridItem,
    Box,
    XButton,
    XDialog,
    Flexbox,
    FlexboxItem
    
  },
  methods: {
      phone(){
          window.location.href = "tel:400-892-6600";
      },
      jifen(){
          this.dialogMsg = '暂不支持移动端查询，请在PC端查看'
          this.dialogVisible = true
          this.dialogLink = ''
      },
      jump(path,query){
          this.$store.dispatch('getUserStatus').then((res) => {
              this.$router.push({path: path, query: query})
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      bao_profile () {
      this.$store.dispatch('bao_profile').then((res) => {
        this.company = res
      }).catch((res) => {
          if (res.errcode === 40001) {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              this.dialogLink = '/login'
          } else if (res.errcode === 40002) {
          }else{
              this.dialogLink = ''
          }
      })
          this.$store.dispatch('getUserStatus').then((res) => {
          }).catch((res) => {
              if (res.errcode === 40001) {
              } else if (res.errcode === 40002) {
                  this.examineVisible = false
              }else if(res.errcode === 40003){
                  this.dialogMsg = res.errmsg
                  this.dialogVisible = true
                  this.dialogLink = '/register1'
              }else{
                  this.dialogLink = ''
              }
          })
    },
      passport_logout () {
          this.$store.dispatch('passport_logout').then((res) => {
            this.$router.push('/')
          }).catch((res) => {
          })
      }
  }
}



