import XHeader from 'vux/src/components/x-header/index.vue'
export default {
  data () {
    return {
      title: ''
    }
  },
  components: {
    XHeader
  },
  methods: {
    back () {
      history.back()
    },
    checkRouter (newVal, oldVal) {
      if (newVal.meta.title) {
        this.title = newVal.meta.title
      } else {
        this.title = ''
      }
    }
  },
  watch: {
    '$route': 'checkRouter'
  },
  beforeMount () {
    if (this.$route.meta.title) {
      this.title = this.$route.meta.title
    } else {
      this.title = ''
    }
  }
}



