import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
export default {
  data () {
    return {
      title: '',
      showToast: false,
      dialogMsg: '',
      dialogLink: '',
        inlink: true,
      dialogVisible: false,
        confirmVisible: false,
        confirmMsg: '',
        confirmLink:'',
      assets: {
          total_assets: '',
          total_yield: '',
          balance: ''
      },
      product_hq: '',
      product_dq: '',
      product_bill: '',
      product_save_pot: '',
      product_bank: '',
        payPassword:[],
        withDrawOpen: false
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
    Cell,
    Group,
    XDialog,
      Confirm
  },
  created () {
      window.scroll(0, 0)
      document.title = '资产概况'
      this.$nextTick(() => {
        this.getAssets()
        this.getProduct()
      })
  },
  beforeRouteLeave(to, from, next){
      if (to.name === 'accountHq' || to.name === 'accountDq' || to.name === 'accountBill' || to.name === 'accountBank' || to.name === 'accountSavePot') {
          from.meta.keepAlive = true
      } else {
          from.meta.keepAlive = false
          window.location.reload()
      }
      next()
  },
  methods: {
      onConfirm(){
          if(!this.confirmLink){
              this.dialogMsg = '未通过企业商户资质审核'
              this.dialogVisible = true
              this.confirmVisible = false
              return
          }
          window.open(this.confirmLink,'_blank')
      },
    jump(path,query){
        this.$store.dispatch('getUserStatus').then((res) => {
             if(path === '/account/cash'){
                 if(!this.withDrawOpen){
                     return
                 }
                 if(this.payPassword.isPayPassword){
                     this.$router.push({path: path, query: query})
                 }else{
                     this.confirmMsg = this.payPassword.msg
                     this.confirmVisible = true
                     this.confirmLink = this.payPassword.url
                 }
             }else{
                 this.$router.push({path: path, query: query})
             }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    getAssets () {
        this.$store.dispatch('getAssets').then((res) => {
          this.assets = res
            this.payPassword = res.payPassword
            this.withDrawOpen = res.withdrawOpen
        }).catch((res) => {
          this.dialogMsg = res.errmsg
          this.dialogVisible = true
          if (res.errcode === 40001) {
            this.dialogLink = '/login'
          } else if (res.errcode === 40002) {
            this.dialogLink = '/register2'
          }else{
            this.dialogLink = ''
          }
        })
    },
    getProduct () {
        this.$store.dispatch('getProduct').then((res) => {
            this.dialogVisible = false
            if(res){
                this.product_hq = res.hq
                this.product_dq = res.dq
                this.product_bill = res.bill
                this.product_save_pot  = res.save_pot
                this.product_bank = res.bank
            }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            }
        })
    },
  }
}



