import SwiperJs from '../../../static/swiper-4.1.0.min.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import swiper1 from '../../assets/images/home/home-slide1-1.jpg'
import icon01 from '../../assets/images/home/icon01.png'
import icon02 from '../../assets/images/home/icon02.png'
import icon03 from '../../assets/images/home/icon03.png'
export default {
  data () {
    return {
      icon01: icon01,
      icon02: icon02,
      icon03: icon03,
      loginStatus: false,
      swiper1: swiper1,
      slides: [],
      qj_product:[],
      modelProduct:[],
      swiper: ''
    }
  },
  components: {
    Swiper,
    SwiperItem,
    Flexbox,
    FlexboxItem,
    XButton
  },
  mounted () {
      window.scroll(0, 0)
      document.title = '首页'
      this.$nextTick(() => {
          this.getBannerInfo()
          this.getLoginStatus()
          this.getQjProduct()
          this.getModelProduct()
      })
  },
  activated () {
      document.title = '首页'
      this.$nextTick(() => {
          this.getLoginStatus()
      })
  },
  created () {
      document.title = '首页'
  },
  methods: {
     jump(router){
         this.$router.push(router)
    },
    getBannerInfo () {
      this.slides = []
      this.$store.dispatch('getBannerInfo').then((res) => {
        this.slides = res
      }).catch((res) => {
        this.dialogMsg = res.errmsg
        this.dialogVisible = true
      })
    },
    getLoginStatus () {
      this.$store.dispatch('getLoginStatus').then((res) => {
        this.loginStatus = res
      }).catch((res) => {
        this.dialogMsg = res.errmsg
        this.dialogVisible = true
      })
    },
    initSwiper () {
      this.swiper = new SwiperJs('.swiper-container', {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        initialSlide: 1,
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true
        }
      })
    },
    getQjProduct () {
        this.$store.dispatch('getQjProduct').then((res) => {
            this.dialogVisible = false
            if(res){
                this.qj_product = res
            }
        }).catch((res) => {
        })
    },
    getModelProduct () {
        this.$store.dispatch('getModelProduct').then((res) => {
            this.dialogVisible = false
            if(res){
                this.modelProduct = res
            }
        }).catch((res) => {
        })
    },

  }
}



