import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import question01Img from '../../assets/images/question01.jpg'
export default {
  data () {
    return {
      tabIndex: 0,
      question01Img: question01Img,
      showAnswer1: false,
      showAnswer2: false,
      showAnswer3: false,
      showAnswer4: false,
      showAnswer5: false,
      showAnswer2_1: false,
      showAnswer2_2: false,
      showAnswer2_3: false,
      showAnswer3_1: false,
      showAnswer3_2: false,
      showAnswer3_3: false,
      showAnswer3_4: false,
      showAnswer3_5: false,
      showAnswer3_6: false,
      showAnswer3_7: false,
      showAnswer3_8: false,
      showAnswer3_9: false,
      showAnswer4_1: false,
      showAnswer4_2: false,
      showAnswer4_3: false,
      showAnswer4_4: false,
      showAnswer4_5: false
    }
  },
  components: {
    XHeader,
    Tab,
    TabItem
  },
  created () {
      document.title = '帮助中心'
  },
  methods: {
    onItemClick (index) {
      this.tabIndex = index
    }
  }
}



