import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      niceProduct:[],
      dialogMsg: '',
      dialogVisible: false,
      dialogLink: ''
    }
  },
  directives: {
      TransferDom
  },
  components: {
    XDialog
  },
  mounted () {
      window.scroll(0, 0)
      this.$nextTick(() => {
          this.getNiceProduct()
      })
  },
  created () {
      document.title = '产品'
  },
  methods: {
    jump(router){
         this.$router.push(router)
    },
    muji(){
         this.dialogMsg = '募集型产品还未上线，敬请期待！'
         this.dialogVisible = true
         this.dialogLink = ''
    },
    getNiceProduct () {
        this.$store.dispatch('getNiceProduct').then((res) => {
            this.dialogVisible = false
            if(res){
                this.niceProduct = res
            }
        }).catch((res) => {
        })
    }

  }
}



