import XHeader from 'vux/src/components/x-header/index.vue'
import asuranceImg1 from '../../assets/images/asurance1.png'
import asuranceImg2 from '../../assets/images/asurance2.png'
export default {
  data () {
    return {
      asuranceImg: asuranceImg1,
      asuranceImg2: asuranceImg2,
        content: ''
    }
  },
  components: {
    XHeader
  },
    beforeCreate () {
        document.title = '企明星产品投资及服务合同'
        this.$nextTick(() => {
            this.tixing_zhuanrangxieyi()
        })
    },
    methods: {
        tixing_zhuanrangxieyi () {
            this.$store.dispatch('tixing_zhuanrangxieyi', {bd_code: this.$route.query.bd_code}).then((res) => {
                this.content = res.xieyi
            }).catch((res) => {
            })
        }
    }
}



