import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
export default {
  data () {
    return {
        title: '',
        dialogMsg: '',
        dialogLink: '',
        inlink: true,
        dialogVisible: false,
        product: {},
        confirmVisible: false,
        confirmMsg: '',
        confirmLink:'',
        disabled: true,
        buy_title: '购买',
        unpaidNo: false,
        payPassword:[]
    }
  },
  components: {
    XButton,
    XHeader,
    XDialog,
      Confirm
  },
    directives: {
        TransferDom
    },
    mounted () {
        document.title = '银行票据理财详情'
        window.scroll(0, 0)
        this.$nextTick(() => {
            this.product_enterprise_star_bill()
        })
    },
  methods: {
      onConfirm(){
          if(!this.confirmLink){
              this.dialogMsg = '未通过企业商户资质审核'
              this.dialogVisible = true
              this.confirmVisible = false
              return
          }
          window.open(this.confirmLink,'_self')
      },
      jump(path,query){
          this.$store.dispatch('getUserStatus').then((res) => {
              this.$router.push({path: path, query: query})
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      unpayOrder(){
          this.$store.dispatch('bao_pay_order', {trade_no:this.unpaidNo}).then((res) => {
              this.confirmMsg = '去新浪收银台完成操作'
              this.confirmVisible = true
              this.confirmLink = res
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.showDialog = true
          })
      },
      goBuy (path,query) {
          this.$store.dispatch('getUserStatus').then((res) => {
              if (this.unpaidNo) {
                  this.unpayOrder()
                  return
              }
              if(this.payPassword.isPayPassword){
                  this.$router.push({path: path, query: query})
              }else{
                  this.confirmMsg = this.payPassword.msg
                  this.confirmVisible = true
                  this.confirmLink = this.payPassword.url
              }

          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogVisible = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              } else if (res.errcode === 40002) {
                  this.dialogLink = '/register2'
              }else{
                  this.dialogLink = ''
              }
          })
      },
      product_enterprise_star_bill () {
      this.$store.dispatch('product_enterprise_star_bill', {sg_code: this.$route.query.sg_code}).then((res) => {
        this.product = res
          this.disabled = !res.buyopen
          this.payPassword = res.payPassword
          if (res.status === 200) {
          }
          else if(res.status === 222){
              this.buy_title = '订单未支付'
              this.unpaidNo = res.unpaidNo
          }
          else if (res.status === 2) {
            this.buy_title = '即将发售'
            this.disabled = true
          } else if (res.status === 4) {
              this.buy_title = '已售出'
              this.disabled = true
          } else if (res.status === 5) {
              this.buy_title = '已预订'
              this.disabled = true
          } else if (res.status === 8) {
              let starttime = res.starttime.substr(5, 6)
              this.buy_title = starttime + '开售'
              this.disabled = true
          } else {
              this.buy_title = '暂未开放'
              this.disabled = true
          }
      }).catch((res) => {
          this.dialogMsg = res.errmsg
          this.dialogVisible = true
          if (res.errcode === 40001) {
              this.dialogLink = '/login'
          } else if (res.errcode === 40002) {
              this.dialogLink = '/register2'
          } else {
              this.dialogLink = ''
          }
      })
    }
  }

}



