import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      title: '',
      amount: '',
        withdraw: {},
      dialogVisible: false,
        dialogLink: '',
        inlink: true,
      dialogMsg: '',
        errMsg: ''
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
    TransferDom
  },
  components: {
    Group,
    XButton,
    XInput,
    XDialog
  },
  created () {
    document.title = '提现'
    this.$nextTick(() => {
      this.bao_withdraw()
    })
  },
  methods: {
  bao_withdraw () {
              this.$store.dispatch('bao_withdraw').then((res) => {
              this.withdraw = res
              }).catch((res) => {
                  if(res.errcode === 40001){
                      this.dialogLink='/login'
                      this.dialogMsg=res.errmsg
                      this.dialogVisible = true
                  }
              })
    },

    setPayPassword(){
        this.$store.dispatch('passport_isPayPassport').then((res) => {
            if(res.isPayPassword){
            }else{
                this.dialogMsg = res.msg
                this.dialogVisible = true
                this.inlink = false
                this.dialogLink = res.url
            }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
        })
    },
    bao_trade_hosting_withdraw () {
        this.errMsg = ''
    if (this.withdraw.isSetPayPassword === -1) {
            this.setPayPassword()
        return 0
    }
        if (this.amount === '') {
            this.dialogMsg = '提现金额不能为空'
            this.dialogVisible = true
            return 0
        }
    if (this.amount >= this.withdraw.available_balance) {
        this.dialogMsg = '提现金额过大'
        this.dialogVisible = true
        return 0
          }
    let form = new FormData()
    form.append('amount', this.amount)
    this.$store.dispatch('bao_trade_hosting_withdraw', form).then((res) => {
        this.dialogMsg = '请在新浪收银台完成操作。如果遇到问题，请致电客服：400-892-6600'
        this.dialogVisible = true
        this.inlink = false
        this.dialogLink = res.url + '?ft=' + res.ft
            }).catch((res) => {
              this.dialogMsg = '失败！' + res.errmsg
              this.dialogVisible = true
            })
    }
  }
}



