import XHeader from 'vux/src/components/x-header/index.vue'
import loginImg from '../../assets/images/login-logo.png'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      loginImg: loginImg,
      showToast: false,
      user_name: '',
      user_pwd: '',
      img_code: '',
      codeImg: '',
      sid: '',
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
      domain: ''
    }
  },
  directives: {
    TransferDom
  },
  components: {
    XHeader,
    Group,
    XInput,
    XButton,
    Toast,
    TransferDom,
    XDialog
  },

    created () {
        document.title = '登录'
        this.$nextTick(()=>{
            this.getDomain ()
            this.getLoginStatus()
            this.sid = Math.random()
            this.img_code=''
        })
    },
  methods: {
    changeImgCode () {
      this.sid = Math.random()
    },
    login () {
      if (this.user_name === '') {
          this.dialogMsg = '请输入手机号'
          this.dialogVisible = true
      } else if (!/^[0-9]{11}$/.test(this.user_name)) {
          this.dialogMsg = '请输入正确的手机号'
          this.dialogVisible = true
      } else if (this.user_pwd === '') {
          this.dialogMsg = '请输入密码'
          this.dialogVisible = true
      } else if (this.img_code === '') {
          this.dialogMsg = '请输入验证码'
          this.dialogVisible = true
      } else {
        let form = new FormData()
        form.append('user_name', this.user_name)
        form.append('user_pwd', this.user_pwd)
        form.append('img_code', this.img_code)
        this.$store.dispatch('login', form).then((res) => {
          history.back()
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            this.changeImgCode()
        })
      }
    },
      getDomain () {
              this.$store.dispatch('passport_getDomain', {}).then((res) => {
                 this.domain = res
              }).catch((res) => {
              })
          },
      checkRouter (newVal, oldVal) {
          if (oldVal.name === 'forgot-pwd') {
              this.showToast = true
          }
      },
      getLoginStatus () {
          this.$store.dispatch('getServerLoginStatus').then((res) => {
              if(res){
               this.$router.push('/')
              }
          }).catch((res) => {

          })
      }
  },


  watch: {
    '$route': 'checkRouter'
  }
}



