import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Box from 'vux/src/components/box/index.vue'
import arrowImg from '../../assets/images/icon-arrow.png'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      showMenu1: false,
      showMenu2: false,
      showNavMask: false,
      arrowImg: arrowImg,
      dialogMsg: '',
      dialogVisible: false,
      dialogLink: '',
    }
  },
  directives: {
      TransferDom
  },
  watch: {
    showNav (newVal, oldVal) {
      if (!newVal) {
        this.showMenu1 = false
        this.showMenu2 = false
      }
    }
  },
  props: {
    showNav: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  computed: {
    ...mapGetters(['company']),
  },
  components: {
    Group,
    Cell,
    Grid,
    GridItem,
    Box,
    XDialog
  },
  methods: {
    jump(path,query){
        this.$store.dispatch('getUserStatus').then((res) => {
            this.$router.push({path: path, query: query})
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    toggleMenu (index) {
      if (index === 1) {
        this.showMenu1 = !this.showMenu1
      } else if (index === 2) {
        this.showMenu2 = !this.showMenu2
      }
    },
      maskClick () {
          this.$emit('maskClick')
      }
}
}



