import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import phoneImg from '../../assets/images/phone.png'
export default {
  data () {
    return {
      title: '',
      phoneImg: phoneImg,
      showFooter: false
    }
  },
  computed: {
  },
  components: {
    Flexbox,
    FlexboxItem
  },
  methods: {
    back () {
      history.back()
    },
    checkRouter (newVal, oldVal) {
      if (newVal.meta.title) {
        this.title = newVal.meta.title
      } else {
        this.title = ''
      }
      this.showFooter = newVal.meta.showFooter
    }
  },
  watch: {
    '$route': 'checkRouter'
  },
  beforeMount () {
    if (this.$route.meta.title) {
      this.title = this.$route.meta.title
    } else {
      this.title = ''
    }
    if (this.$route.meta.showFooter) {
      this.showFooter = this.$route.meta.showFooter
         } else {
           this.showFooter = false
         }
  }
}



