import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
export default {
  data () {
    return {
      title: '',
      list: [
          {key: 1, value: '本息赎回至余额账户'},
          {key: 2, value: '本息赎回至银行卡'},
          {key: 3, value: '本金续投该定期'},
          {key: 4, value: '部分续投定期,部分转为活期'}
      ],
      currentMenu: 1,
      redeem: '',
      menuValue: '本息赎回至余额账户',
      menuKey: 1,
      openSelect: false,
      showDialog: false,
      showConfirm: false,
      confirmMessage: '',
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
    Cell,
    Group,
    XButton,
    Selector,
    XInput,
    XDialog,
    Confirm
  },
  created () {
      document.title = '到期赎回设置'
      this.$nextTick(() => {
          this.getRedeemConfig()
      })
  },
  methods: {
    submit () {
        if(this.menuKey === 4){
            if (this.redeem.amount_dingqi > 0) {
                if (this.redeem.amount_dingqi % this.redeem.dingqi_dizeng != 0 || this.redeem.amount_dingqi < this.redeem.dingqi_qitou || this.redeem.amount_dingqi > this.redeem.dingqi_ketou || this.redeem.amount_dingqi > this.redeem.amount_total) {
                    this.dialogMsg = '定期金额输入错误，起投'+this.redeem.dingqi_qitou+'元，可投'+this.redeem.dingqi_ketou+'元'
                    this.dialogVisible = true
                    return
                }
            }
            if (this.redeem.amount_huoqi > 0) {
                if (this.redeem.amount_huoqi % this.redeem.huoqi_dizeng != 0 || this.redeem.amount_huoqi < this.redeem.huoqi_qitou || this.redeem.amount_huoqi > this.redeem.huoqi_ketou || this.redeem.amount_huoqi > this.redeem.amount_total) {
                    this.dialogMsg = '活期金额输入错误，起投'+this.redeem.huoqi_qitou+'元，可投'+this.redeem.huoqi_ketou+'元'
                    this.dialogVisible = true
                    return
                }
            }
        }

        if (this.menuKey === 1 || this.menuKey === 2) {
            this.confirmMessage = '该定期到期后，' + this.redeem.amount_total + '元'
        } else if (this.menuKey === 3) {
            this.confirmMessage = '该定期到期后，本金' + this.redeem.amount + '元继续投资' + this.redeem.goods_name + '，利息' + this.redeem.total_yield + '元'
        } else if (this.menuKey === 4) {
            this.confirmMessage = '该定期到期后，' + this.redeem.amount_dingqi + '元续投' + this.redeem.goods_name + '，' + this.redeem.amount_huoqi + '元投资企明星活期，' + this.redeem.amount_balance + '元'
        }
        if ((this.redeem.default_payee_type === 'BINKCARD' || this.menuKey === 2) && this.menuKey !== 1) {
            this.confirmMessage += '赎回到银行卡' + '【' + this.redeem.bank_name + '(' + this.redeem.weihao + ') 】'
        } else {
            this.confirmMessage += '赎回到余额账户'
        }
        this.showConfirm = true
    },
    onConfirm () {
        var model = ''
        if (this.menuKey === 1) {
            model = 'd2b'
        } else if (this.menuKey === 2) {
            model = 'd2c'
        } else if (this.menuKey === 3) {
            model = 'd2d'
        } else if (this.menuKey === 4) {
            model = 'd2h'
        }
        this.showConfirm = false
        let form = new FormData()
        form.append('out_trade_no', this.redeem.out_trade_no)
        form.append('model', model)
        form.append('amount_balance', this.redeem.amount_balance)
        form.append('amount_dingqi', this.redeem.amount_dingqi)
        form.append('amount_huoqi', this.redeem.amount_huoqi)
        this.$store.dispatch('postRedeemConfig', form).then((res) => {
            this.showDialog = true
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    ok () {
        this.showDialog = false
    },
    getRedeemConfig () {
        this.redeem = {}
        this.$store.dispatch('getRedeemConfig', {'goods_id': this.$route.query.goodsId, 'out_trade_no': this.$route.query.outTradeNo}).then((res) => {
            this.redeem = res
            this.menuKey = res.transfer_set_key
            this.menuValue = res.transfer_set_value
            if (!res.llpay) {
                this.list = [{key: 1, value: '本息赎回至余额账户'}, {key: 3, value: '本金续投该定期'}, {key: 4, value: '部分续投定期,部分转为活期'}]
            }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    checkD2b () {
      this.redeem.amount_total = Number(this.redeem.amount_total)
      this.redeem.amount_huoqi = Number(this.redeem.amount_huoqi)
      this.redeem.amount_dingqi = Number(this.redeem.amount_dingqi)
      if ((this.redeem.amount_huoqi + this.redeem.amount_dingqi) > this.redeem.amount_total) {
          this.redeem.amount_balance = 0
      } else {
          this.redeem.amount_balance = this.redeem.amount_total * 1000 - this.redeem.amount_huoqi * 1000 - this.redeem.amount_dingqi * 1000
          this.redeem.amount_balance = this.redeem.amount_balance / 1000
      }
    }
  }
}



