import {mapGetters} from 'vuex'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
export default {
  data () {
    return {
      title: '',
        prod_title:'产品',
        month_title:'月份',
        selectTag: 0,
        buy_type: [],
        buy_month: [],
        month_array:[],
        total_yield: '',
        yesterday_yield: '',
        selectProd: this.$route.query.type,
        selectMonth: '',
        selectMonthPage: '',
        monthDetailArr:[],
        more: 0
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  components: {
    Grid,
    GridItem,
    Group,
    Cell,
    CellFormPreview
  },
  created () {
      document.title = '收益明细'
      window.scroll(0, 0)
      this.$nextTick(() => {
          this.bao_getMonthProceedSearch()
          this.bao_proceed()
      })
  },
  methods: {
      bao_proceed () {
          this.$store.dispatch('bao_proceed', {goods_type: this.selectProd}).then((res) => {
              this.total_yield = res.total_yield,
              this.yesterday_yield = res.yesterday_yield
          }).catch((res) => {
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },
      bao_getMonthProceed () {
          this.monthDetailArr = []
          this.$store.dispatch('bao_getMonthProceed',{month: this.selectMonth, goods_type: this.selectProd}).then((res) => {
              if(this.selectMonth != '' || this.month_array.length <= 1){
                  this.more = 0
              }else{
                  this.more = 1
              }
              this.monthDetailArr = this.monthDetailArr.concat(res)
          }).catch((res) => {
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },
      bao_getMonthProceedSearch () {
          this.$store.dispatch('bao_getMonthProceedSearch',{goods_type: this.selectProd}).then((res) => {
              this.buy_type = res.buy_type
              this.buy_month = res.buy_month
              this.month_array = []
              this.selectMonthPage = res.buy_month[0]
              let save = 0
              for (let x in res.buy_month) {
                  this.month_array.push(res.buy_month[x])
                  if (this.selectMonth == res.buy_month[x]) {
                      save = 1
                  }
              }
              if (save === 0) {
                  this.selectMonth = ''
              }
              this.allPage()
          }).catch((res) => {
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },
      allPage () {
          this.monthDetailArr = []
          this.$store.dispatch('bao_getMonthProceed',{goods_type: this.selectProd, month: this.selectMonth}).then((res) => {
              this.monthDetailArr = this.monthDetailArr.concat(res)
              if(this.selectMonth != '' || this.month_array.length <= 1){
                  this.more = 0
              }else{
                  this.more = 1
              }
          }).catch((res) => {
              this.more = 0
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },
      nextPage () {
          let index = this.month_array.indexOf(this.selectMonthPage)
          if(index+1>this.month_array.length-1){
              this.more = 0
              return
          }

          this.selectMonthPage = this.month_array[index+1]
          this.$store.dispatch('bao_getMonthProceed',{month: this.selectMonthPage, goods_type: this.selectProd}).then((res) => {
              this.monthDetailArr = this.monthDetailArr.concat(res)
              this.more = 1
          }).catch((res) => {
              this.more = 0
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },
  }
}



