import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
export default {
  data () {
    return {
      dialogMsg: '',
      dialogLink: '',
      dialogVisible: false,
      selectTag1: false,
      selectTag2: false,
      selectTag3: false,
      listType: [],
      listTypeKey: '',
      listTypeValue: '类型',
      listGoods: [],
      listGoodsKey: 'all',
      listGoodsValue: '标的',
      listMonth: [],
      listMonthKey: '',
      listMonthValue: '月份',
      product: [],
      page: 1,
      moreData: true
    }
  },
  computed: {
    ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
    Cell,
    Group,
    Grid,
    GridItem,
    XDialog
  },
  created () {
      document.title = '交易明细'
      this.$nextTick(() => {
          this.product = []
          if(this.$route.query.bd_code){
              this.listGoodsKey = this.$route.query.bd_code
          }
          this.getTradeRecordSearch()
      })
  },
  beforeRouteLeave(to, from, next){
      if (to.name === 'orderDetail') {
          from.meta.keepAlive = true
      } else {
          from.meta.keepAlive = false
          window.location.reload()
      }
      next()
  },
  methods: {
    changeFilter (val) {
      this.selectTag = val
    },
    getTradeRecord () {
      this.product = {}
      this.$store.dispatch('getTradeRecord', {'page': 1, 'order_type': this.listTypeKey, 'bd_code': this.listGoodsKey, 'month': this.listMonthKey}).then((res) => {
          this.page = res.page
          this.moreData = res.next_page
          this.product = res.data
      }).catch((res) => {
          this.dialogMsg = res.errmsg
          this.dialogVisible = true
          if (res.errcode === 40001) {
              this.dialogLink = '/login'
          } else if (res.errcode === 40002) {
              this.dialogLink = '/register2'
          }else{
              this.dialogLink = ''
          }
          if (res.errcode === 40004) {
              this.moreData = false
          }
      })
    },
    getTradeRecordSearch (index) {
        this.$store.dispatch('getTradeRecordSearch', {'order_type': this.listTypeKey, 'bd_code': this.listGoodsKey, 'month': this.listMonthKey}).then((res) => {
            if (index === 'type') {
                if (this.listTypeKey === 'chongzhi' || this.listTypeKey === 'tixian') {
                    this.listGoodsValue = '-'
                } else if (this.listGoodsKey != 'all') {
                    for (let i=0;i<res.list_goods.length;i++) {
                        if (this.listGoodsKey == res.list_goods[i].key) {
                            this.listGoodsValue = res.list_goods[i].value
                        }
                    }
                } else {
                    this.listGoodsValue = '标的'
                }
            }
            this.listType = res.list_type
            this.listGoods = res.list_goods
            this.listMonth = res.list_month
            if (this.listGoodsKey) {
                if(this.listGoodsKey === 'all'){
                    this.listGoodsValue = '标的'
                }else{
                    for (let x in this.listGoods){
                        if(this.listGoods[x]['key'].indexOf(this.listGoodsKey) > -1){
                            this.listGoodsValue = this.listGoods[x]['value']
                        }
                    }
                }
            }
            if(this.listMonthKey){
                let save = 0
                for (let x in res.list_month) {
                    if (this.listMonthKey == res.list_month[x].value) {
                        save = 1
                    }
                }
                if (save === 0) {
                    this.listMonthKey = ''
                    this.listMonthValue = '月份'
                }
            }
            this.$forceUpdate()
            this.getTradeRecord();
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    nextPage () {
        this.page = Number(this.page) + 1
        this.$store.dispatch('getTradeRecord', {'page': this.page, 'order_type': this.listTypeKey, 'bd_code': this.listGoodsKey, 'month': this.listMonthKey}).then((res) => {
            this.page = res.page
            this.moreData = res.next_page
            for (var index in res.data) {
                if (this.product[index]) {
                    this.product[index] = this.product[index].concat(res.data[index])
                } else {
                    this.product[index] = []
                    this.product[index] = res.data[index]
                }
                this.$forceUpdate()
            }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
            if (res.errcode === 40004) {
                this.moreData = false
            }
        })
    }
  }
}



