import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'
import 'echarts/lib/component/tooltip'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
  data () {
    return {
      title: '',
      tabIndex: 0,
      lineChart1: null,
      lineChart2: null,
      chartOptions1: {},
      chartOptions2: {},
      dialogMsg: '',
      dialogVisible: false,
      dialogLink: '',
        inlink: true,
        confirmVisible: false,
        confirmMsg: '',
        confirmLink:'',
      product: {},
      chart: {},
        payPassword:[]
    }
  },
  computed: {
  },
  directives: {
      TransferDom
  },
  components: {
    Tab,
    TabItem,
    XButton,
    XHeader,
    XDialog,
      Confirm
  },
  methods: {
      onConfirm(){
          if(!this.confirmLink){
              this.dialogMsg = '未通过企业商户资质审核'
              this.dialogVisible = true
              this.confirmVisible = false
              return
          }
          window.open(this.confirmLink,'_blank')
      },
      jump(path,query){
        this.$store.dispatch('getUserStatus').then((res) => {
            if(this.payPassword.isPayPassword){
                this.$router.push({path: path, query: query})
            }else{
                this.confirmMsg = this.payPassword.msg
                this.confirmVisible = true
                this.confirmLink = this.payPassword.url
            }
        }).catch((res) => {
            this.dialogMsg = res.errmsg
            this.dialogVisible = true
            if (res.errcode === 40001) {
                this.dialogLink = '/login'
            } else if (res.errcode === 40002) {
                this.dialogLink = '/register2'
            }else{
                this.dialogLink = ''
            }
        })
    },
    onItemClick (index) {
      this.tabIndex = index
      if (index === 1) {
        setInterval(() => {
          this.chartOptions2 = {
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: this.chart.date
            },
            yAxis: {
              type: 'value',
              axisLabel: {
                formatter: '{value}元'
              }
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross'
              }
            },
            series: [{
              name: '万份收益',
              data: this.chart.wfjz,
              type: 'line'
            }]
          }
          this.lineChart2 = echarts.init(document.getElementById('lineChart2'))
          this.lineChart2.setOption(this.chartOptions2)
        }, 0)
      }
    },
    throttle (fn, delay) { // 节流函数
      var timer = null
      return function () {
        clearTimeout(timer)
        timer = setTimeout(function () {
          fn()
        }, delay)
      }
    },
    getMonetaryFund () {
    this.$store.dispatch('getMonetaryFund').then((res) => {
      this.product = res
        this.payPassword = res.payPassword
    }).catch((res) => {
      this.dialogMsg = res.errmsg
      this.dialogVisible = true
        if (res.errcode === 40001) {
            this.dialogLink = '/login'
        } else if (res.errcode === 40002) {
            this.dialogLink = '/register2'
        }else{
            this.dialogLink = ''
        }
    })
  },
    getMonetaryFundChart () {
    this.$store.dispatch('getMonetaryFundChart').then((res) => {
        this.chart = res
        this.lineChart1.setOption({
            xAxis: {
                data: res.date
            },
            series: {
                data: res.seven_yield
            }
        })
    }).catch((res) => {
      this.dialogMsg = res.errmsg
      this.dialogVisible = true
        if (res.errcode === 40001) {
            this.dialogLink = '/login'
        } else if (res.errcode === 40002) {
            this.dialogLink = '/register2'
        }else{
            this.dialogLink = ''
        }
    })
  }
  },
  created () {
      document.title = '汇添富现金宝货币基金详情'
      window.scroll(0, 0)
      this.$nextTick(() => {
          this.getMonetaryFund()
          this.getMonetaryFundChart()
      })
  },
  mounted () {
    document.title = '汇添富现金宝货币基金详情'
    this.chartOptions1 = {
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: this.chart.date
      },
      yAxis: {
        type: 'value',
        axisLabel: {
          formatter: '{value}%'
        }
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross'
        }
      },
      series: [{
        name: '七日年化收益率',
        type: 'line',
        data: this.chart.seven_yield
      }]
    }
    this.lineChart1 = echarts.init(document.getElementById('lineChart1'))
    this.lineChart1.setOption(this.chartOptions1)
    window.onresize = this.throttle(() => {
      this.lineChart1.resize()
      this.lineChart2.resize()
    }, 500)
    if (this.$route.meta.title) {
      this.title = this.$route.meta.title
    } else {
      this.title = ''
    }
  }
}



