import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import statusBar from '../home/statusBar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
export default {
    data () {
        return {
            title: '',
            dialogShow: false,
            dialogMsg: '',
            dialogLink: '',
            order_type: '',
            button_msg: '返回企明星账户',
            step: 0,
            content_all: {
                desc1: '',
                time1: '',
                desc2: '',
                time2: '',
                desc3: '',
                time3: ''
            }
        }
    },
    computed: {
        ...mapGetters(['company'])
    },
    directives: {
        TransferDom
    },
    components: {
        Group,
        Flexbox,
        FlexboxItem,
        XButton,
        statusBar,
    },
    created () {
        document.title = '中子星金融'
        this.$nextTick(() => {
            this.getDetail()
        })
    },
    methods: {
        getDetail () {
            this.$store.dispatch('sinapay_index', {'trade_type':this.$route.query.trade_type,'trade_no':this.$route.query.trade_no,'sign': this.$route.query.sign}).then((res) => {

                    if(res.order_status === 'done'){
                        this.step = 1
                       this.title = '申请购买'+res.goods_name+'成功'
                        this.content_all.time2 = res.yield_date+'('+res.yield_week+')'
                        this.content_all.time3 = res.show_yield_date+'('+res.show_yield_week+')'
                    } else if(res.order_status === 'ing') {
                        this.title = '银行确认中，请稍后刷新查看'
                    } else {
                        this.button_msg = '请重新购买'
                    }
                  this.content_all.desc1 = '购买成功'
                  this.content_all.desc2 = '开始计算收益'
                  this.content_all.desc3 = '第一笔收益到账'
                  this.content_all.time1 = res.create_time
                 let now = new Date()
                 let yield_date = new Date(res.yield_date)
                 let show_yield_date = new Date(res.show_yield_date)
                if(yield_date<now){
                    this.step = 2
                }
                if(show_yield_date<now){
                    this.step = 3
                }
            }).catch((res) => {
                this.dialogMsg = res.errmsg
                this.dialogShow = true
                if (res.errcode === 40001) {
                    this.dialogLink = '/login'
                }
            })
        }
    }
}



